@mixin decors {

	&::before,
	&::after {
		content: '';
		position: absolute;

		aspect-ratio: 1;
		border-radius: 50%;

		background-color: $white;
		pointer-events: none;
	}
}

.timer_banner {
	@include decors;
	@include z-index(timer-banner);

	top: var(--height-header);
	left: 0;

	width: 100%;
	overflow: hidden;

	font-family: $font_1;
	color: $white;

	.body--shopify_banner_mod & {
		@include bp($point_2, min) {
			top: calc(var(--height-header) + 4.6rem);
		}

		@include bp($point_3) {
			top: calc(var(--height-header) + 10.9rem);
		}
	}

	@include bp($point_2, min) {
		position: fixed;
		padding: .9rem var(--gl-hr-indent);
	}

	@include bp($point_3) {
		position: absolute;
	}

	@include bp($point_3, $point_6) {
		padding: 1.5rem var(--gl-hr-indent) 1.2rem 7.4rem;
	}

	@include bp($point_6 - 1) {
		padding: 1.5rem var(--gl-hr-indent) 1.2rem 6.8rem;
	}

	&::before,
	&::after {
		@include bp($point_2, min) {
			opacity: 0.4;
		}

		@include bp($point_3) {
			opacity: 0.3;
		}
	}

	&::before {
		@include bp($point_2, min) {
			top: 1.4rem;
			right: .7%;

			width: 0.6rem;
		}

		@include bp($point_3) {
			bottom: 1rem;
			right: 2rem;

			width: 0.4rem;
		}
	}

	&::after {
		width: 1rem;

		@include bp($point_2, min) {
			bottom: -3px;
			right: 1.68%;
		}

		@include bp($point_3) {
			bottom: 43%;
			left: 8rem;
		}
	}

	.body--color_v1_mod & {
		background: linear-gradient(90deg, #9d18ff 0%, #fb1e7b 100%);
	}

	.body--color_v2_mod & {
		background: linear-gradient(135deg, #079d16 0%, #81ac08 100%);
	}

	.body--color_v3_mod & {
		background: linear-gradient(138deg, #3484e2 0%, #2dccef 100%);
	}

	.body--color_v4_mod & {
		background: linear-gradient(90deg, #c7150a 0%, #fc6e31 100%);
	}

	.body--color_v5_mod & {
		background: linear-gradient(90deg, #ed6604 0%, #ee9d01 100%);
	}

	.section_in {
		&::before {
			@include bp($point_2, min) {
				content: '';
				position: absolute;
				top: 2.6rem;
				left: 52.8%;

				width: .4rem;
				aspect-ratio: 1;
				border-radius: 50%;

				background-color: rgba($white, .3);
				pointer-events: none;
			}
		}
	}
}

.timer_banner__row {
	@include decors;

	@include bp($point_2, min) {
		display: flex;
		align-items: center;
		gap: 4rem;
	}

	&::before,
	&::after {
		opacity: 0.3;
	}

	&::before {
		@include bp($point_2, min) {
			top: 0;
			left: 12.5%;

			width: 0.8rem;
		}

		@include bp($point_3) {
			top: 0;
			left: 48%;

			width: .8rem;
		}
	}

	&::after {
		@include bp($point_2, min) {
			bottom: -3px;
			left: 5.5%;

			width: 1rem;
		}

		@include bp($point_3) {
			top: .5rem;
			right: 3rem;

			width: 0.4rem;
		}
	}
}

.timer_banner__column {
	&:last-child {
		@include bp($point_2, min) {
			margin-left: auto;
		}
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}
}

.timer_banner__content {
	display: flex;
	align-items: center;
	gap: 1.6rem;
}

.timer_banner__icon {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-shrink: 0;

	background-color: var(--theme-color);
	box-shadow: 0 2px 1.8rem 0 rgba($black, 0.05);

	@include bp($point_2, min) {
		width: 4.6rem;
		height: 4.6rem;
		padding: .8rem;

		border-radius: .8rem;
	}

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;

		display: flex;
		align-items: center;
		justify-content: center;

		width: 5.4rem;
		height: 100%;
		padding: 1.3rem;
	}

	.body--color_v1_mod & {
		background: #7e03ca;
	}

	.body--color_v2_mod & {
		background: #107d1b;
	}

	.body--color_v3_mod & {
		background: #0868c4;
	}

	.body--color_v4_mod & {
		background: #ac1107;
	}

	.body--color_v5_mod & {
		background: #fb8c3b;
	}
}

.timer_banner__picture {
	display: block;

	@include bp($point_2, min) {
		width: 100%;
		height: 100%;
	}

	@include bp($point_3) {
		width: 2.8rem;
		height: 2.8rem;
	}
}

.timer_banner__img {
	@include contain_img;

	aspect-ratio: 1;
}

.timer_banner__title {
	font-weight: 600;

	@include bp($point_2, min) {
		font-size: 2.2rem;
		line-height: 1;
	}

	@include bp($point_3) {
		line-height: 1.4;
	}

	@include bp($point_3, $point_6) {
		max-width: 95%;
		font-size: 2rem;
	}

	@include bp($point_6 - 1) {
		font-size: 1.6rem;
	}
}

.timer_banner__body {
	display: flex;

	@include bp($point_2, min) {
		gap: 3.3rem;
	}

	@include bp($point_3) {
		gap: 1.6rem;
	}
}

.timer_banner__item {
	text-align: center;

	@include bp($point_3) {
		display: flex;
		align-items: center;
		gap: 3px;
	}
}

.timer_banner__value {
	font-weight: 600;

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.2;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		line-height: 1.5;
	}
}

.timer_banner__label {
	font-size: 1.4rem;
	text-transform: uppercase;

	@include bp($point_2, min) {
		line-height: 1.14;
	}

	@include bp($point_3) {
		line-height: 1;
	}
}
