.error_section {
	@include bp($point_3) {
		padding-top: 1.4rem;
		padding-bottom: 0;
	}
}

.error_section__row {
	@include bp($point_2, min) {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
	}
}

.error_section__column {
	position: relative;

	&:nth-child(2n+1) {
		@include bp($point_2, min) {
			order: 1;
			width: 58%;
		}
	}

	&:nth-child(2n+2) {
		@include bp($point_2, min) {
			order: 0;
			width: 42%;
		}
	}
}

.error_section__content {
	@include bp($point_2, min) {
		padding-top: 5.4rem;
	}

	@include bp($point_3) {
		text-align: center;
	}

	a,
	button {
		@include bp($point_3) {
			width: 100%;
		}
	}
}

.error_section__media {
	@include bp($point_2, min) {
		max-width: 72.6rem;
		max-height: 39.2rem;
	}

	@include bp($point_3) {
		max-width: 60rem;
		margin: 0 auto -19.8vw;
	}
}

.error_section__picture {
	position: relative;

	display: block;
	width: 100%;
	min-width: 43.4rem;

	@include bp($point_2, min) {
		left: 6.6rem;
		top: -3rem;
		padding-bottom: 99.8%;
	}

	@include bp($point_3) {
		left: calc(50% - .6rem);
		transform: translateX(-50%);
		padding-bottom: 130%;
	}
}

.error_section__img {
	@include contain_img;

	position: absolute;
	top: 0;
	left: 0;
}

.error_section__label {
	@include category_label;

	line-height: 1.57;
	color: $black_8;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 0.8rem;
		}
	}
}

.error_section__title {
	@include h1;

	color: $black_2;

	@include bp($point_3) {
		font-size: 2.4rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}
}

.error_section__text {
	@include text_3;


	max-width: 96%;
	color: $black_3;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.8rem;
		}
	}
}