.contact_form {
}

.contact_form__inputs {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.contact_form__inputs_list {
	@include bp($point_4, min) {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: -2.4rem;
		margin-right: -1.8rem;
	}
}

.contact_form__input {
	width: 100%;

	@include bp($point_4, min) {
		padding-bottom: 2.4rem;
		padding-right: 1.8rem;
	}

	&.contact_form__input--half_size_mod {
		@include bp($point_4, min) {
			width: 50%;
		}
	}

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			margin-bottom: 2rem;
		}
	}
}

.contact_form__select {
	position: relative;
}

.contact_form__select_btn {
	position: relative;
	width: 100%;
	height: 4rem;
	padding: 0 3.4rem 0 1rem;

	font-family: inherit;
	font-size: 1.6rem;
	text-align: left;
	color: $black_6;

	border: 1px solid $gray_6;
	border-radius: 0.8rem;
	box-shadow: 0 2px 2px rgba(0, 0, 0, 0.02);
	background-color: $white;

	transition: border-color 0.3s ease, box-shadow 0.3s ease;
	cursor: pointer;

	&:hover,
	&:focus {
		border-color: $primary;
		box-shadow: 0 0 0 2px rgba(5, 145, 255, 0.1);
	}

	&::after {
		position: absolute;
		content: '';
		z-index: 1;
		bottom: 1rem;
		right: 1rem;
		display: block;
		width: 1.6rem;
		height: 1.6rem;
		background-image: url('data:image/svg+xml;base64,');
		transition: transform 0.2s ease;
	}

	&.contact_form__select_btn--checked_state {
		color: $black_2;
	}

	&.contact_form__select_btn--active_state {
		&::after {
			transform: rotate(180deg);
		}
	}
}

.contact_form__select_list {
	position: absolute;
	z-index: 10;
	top: calc(100% + 0.6rem);
	left: 0;
	width: 100%;
	padding: 4px;

	border-radius: 0.6rem;
	background-color: $white;
	box-shadow: 0 0.9rem 2.8rem 0.8rem rgba(0, 0, 0, 0.05), 0 3px 0.6rem -4px rgba(0, 0, 0, 0.12),
		0 0.6rem 1.6rem 0 rgba(0, 0, 0, 0.08);
	pointer-events: none;
	opacity: 0;
	transform: translateY(-10%);
	will-change: transform;

	transition: opacity 0.3s ease, transform 0.3s ease;

	&.contact_form__select_list--open_state {
		pointer-events: initial;
		opacity: 1;
		transform: translateY(0);
	}
}

.contact_form__btn {
	width: 100%;
}
