@mixin cover_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

@mixin contain_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

@mixin popup {
	@include flex_block(row, nowrap, flex-start);

	position: fixed;
	top: 0;
	left: 0;

	width: 100%;
	height: 100vh;
	padding: 6rem var(--gl-indent);

	overflow-y: auto;

	background-color: rgba($black, 0.75);

	opacity: 0;
	pointer-events: none;
	transition: opacity 0.3s; //      "const timeout" (300)  initPopup.js

	z-index: 1000;

	&.active_state {
		opacity: 1;
		pointer-events: initial;
	}
}

@mixin word_decor {
	position: relative;

	&::after {
		content: '';
		position: absolute;
		z-index: -1;

		@include bp($point_2, min) {
			top: 80%;
			left: 0;
			width: 98%;
			height: 2.2rem;
		}

		@include bp($point_3) {
			top: 74%;
			left: 2px;
			width: 99%;
			height: 1.6rem;
		}

		background-repeat: no-repeat;
		background-image: url(data:image/svg+xml;base64,);
	}
}

@mixin word_decor_2 {
	position: relative;

	&::after {
		content: '';
		position: absolute;
		z-index: -1;
		top: 74%;
		left: 50%;

		width: 110%;

		transform: translateX(-50%);

		@include bp($point_2, min) {
			height: 2.2rem;
		}

		@include bp($point_3) {
			height: 1.6rem;
		}

		background-repeat: no-repeat;
		background-image: url(data:image/svg+xml;base64,);
	}
}
