.features {
	&.features--no_indent_mod {
		@include bp($point_2, min) {
			padding-top: 0;
		}

		@include bp($point_3) {
			padding-top: 0;
		}
	}
}

.features__heading {
	&:not(:last-child) {
		@include bp($point_2,min){
			margin-bottom: 3.5rem;
		}

		@include bp($point_3){
			margin-bottom: 2.2rem;
		}
	}
}

.features__row {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch);

		margin: 0 -2.4rem -2.4rem 0;
	}

	.features--v2_mod & {
		@include bp($point_3, $point_5) {
			@include flex_block(row, wrap, flex-start, stretch);

			margin: 0 -1.6rem -1.6rem 0;
		}
	}
}

.features__col {
	display: flex;

	@include bp($point_2, min) {
		flex-shrink: 0;
		width: 50%;
		padding: 0 2.4rem 2.4rem 0;
	}

	&.features__col--full_mod {
		@include bp($point_2, min) {
			width: 100%;
		}
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}

	.features--v2_mod & {
		@include bp($point_2, min) {
			width: 33.333%;
			padding: 0 2.4rem 2.4rem 0;
		}

		@include bp($point_3, $point_5) {
			padding: 0 1.6rem 1.6rem 0;
			width: 50%;
		}

		@include bp($point_5 - 1) {
			width: 100%;
		}
	}
}
