$gap: 1.2rem;
$hr_padding: .4rem;

.bread_crumbs {
	margin-left: calc($hr_padding * -1);

	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}
}

.bread_crumbs__list {
	display: flex;
	gap: calc($gap - $hr_padding);
	flex-wrap: wrap;
}

.bread_crumbs__item {
	@include text_2;

	display: flex;
	gap: calc($gap - $hr_padding);

	color: rgba($black, .45);

	&:not(:last-child) {
		&::after {
			content: '/';
		}
	}
}

.bread_crumbs__link {
	color: inherit;
	transition: background .3s;

	border-radius: .4rem;
	padding: 0 $hr_padding;

	@at-root {
		a#{&} {

			&:hover,
			&:focus {
				text-decoration: none;
				background-color: $black_4;
			}
		}
	}
}