.cool-perks__row {
	@include bp($point_2, min) {
		margin: 0 -2.4rem -2.4rem 0;
	}

	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, stretch);
	}

	@include bp($point_3) {
		margin: 0 -1.6rem -1.6rem 0;
	}
}

.cool_perks__column {
	@include bp($point_2, min) {
		width: 33.333%;
		padding: 0 2.4rem 2.4rem 0;
	}

	@include bp($point_4, min) {
		flex-shrink: 0;
	}

	@include bp($point_3) {
		padding: 0 1.6rem 1.6rem 0;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
		flex-grow: 1;
	}
}

.cool_perks__card {
	position: relative;

	display: flex;
	flex-direction: column;

	border: 1px solid $border_secondary;
	background: rgba($black, 0.02);
	border-radius: $gl_radius;

	overflow: hidden;

	@include bp($point_2, min) {
		min-height: 45.4rem;
	}

	@include bp($point_4, min) {
		height: 100%;
	}

	@include bp($point_3, $point_7) {
		min-height: 42.5rem;
	}

	&.cool_perks__card--v1_mod {
		@include bp($point_4, min) {
			padding-bottom: 2.2rem;
		}

		@include bp($point_4 - 1) {
			padding-bottom: 2rem;
		}
	}
}

.cool_perks__decor_in {
	position: absolute;

	.cool_perks__card--v1_mod & {
		@include bp($point_4, min) {
			bottom: -12rem;
			left: -4.2rem;
			width: 28.8rem;
			height: 28.8rem;
		}

		@include bp($point_4 - 1) {
			bottom: -5.8rem;
			left: -2rem;
			width: 19.6rem;
			height: 19.6rem;
		}
	}

	.cool_perks__card--v2_mod & {
		left: -2rem;
		width: 15rem;
		height: 15rem;

		@include bp($point_4, min) {
			bottom: 3rem;
		}

		@include bp($point_4 - 1) {
			bottom: 2px;
		}
	}

	.cool_perks__card--v3_mod & {
		@include bp($point_4, min) {
			bottom: -1rem;
			left: -8.6rem;
			width: 24.5rem;
			height: 24.5rem;
		}

		@include bp($point_4 - 1) {
			bottom: -2.2rem;
			left: -5.5rem;
			width: 17.5rem;
			height: 17.5rem;
		}
	}

	&.cool_perks__decor_in--1_mod {
		@include bp($point_4, min) {
			bottom: 6rem;
			left: 23rem;
			width: 24.5rem;
			height: 24.5rem;
		}

		@include bp($point_4 - 1) {
			bottom: 9.6rem;
			left: 21.2rem;
			width: 17.5rem;
			height: 17.5rem;
		}
	}
}

.cool_perks__heading {
	position: relative;
	z-index: 1;

	@include bp($point_2, min) {
		padding: 3.2rem 3.2rem 2.5rem;
	}

	@include bp($point_3) {
		padding: 2.4rem 2.4rem 2rem;
	}
}

.cool_perks__title {
	@include h3;

	color: rgba($black, 0.88);

	@include bp($point_2, min) {
		max-width: 24.8rem;
	}

	@include bp($point_3) {
		max-width: 26rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 0.8rem;
		}
	}
}

.cool_perks__text {
	font-size: 1.6rem;
	line-height: 1.5;
	color: rgba($black, 0.65);
}

.cool_perks__bottom {
	position: relative;
	z-index: 1;

	display: flex;
	flex-grow: 1;
}

.cool_perks__img_w {
	position: relative;

	display: block;

	.cool_perks__card--v1_mod & {
		width: 100%;
		aspect-ratio: 918/400;
		margin: auto 0;

		@include bp($point_4, min) {
			width: calc(100% + 5.2rem);
			margin-left: -2.6rem;
			margin-right: -2.6rem;
		}
	}

	.cool_perks__card--v2_mod & {
		margin-left: auto;
		aspect-ratio: 644/542;

		@include bp($point_4, min) {
			width: 79.8%;
		}

		@include bp($point_4 - 1) {
			width: 77.7%;
		}
	}

	.cool_perks__card--v3_mod & {
		margin: 0 auto;
		aspect-ratio: 596/530;

		@include bp($point_4, min) {
			width: 73.9%;
		}

		@include bp($point_4 - 1) {
			width: 79.3%;
		}
	}
}

.cool_perks__img_in {
	@include stretch;
	@include contain_img;

	object-position: bottom;

	filter: drop-shadow(0 4px 1.2rem rgba(0, 0, 0, 0.05));
}
