.calculator {
	display: flex;
	flex-direction: column;
	align-items: center;
	max-width: 37.4rem;
	margin: 0 auto;

	text-align: center;

	background-color: $white;
	border: 1px solid $border_secondary;
	border-radius: $gl_radius_2;
	box-shadow: 0 3px 2.7rem 0 $black_4;

	@include bp($point_2, min) {
		gap: 2.4rem;
		padding: 3.4rem 3rem 3.4rem;
	}

	@include bp($point_3) {
		gap: 1.6rem;
		padding: 2.5rem 3rem;
	}

	.sec_calculator & {
		@include bp($point_5) {
			padding: 2.4rem 2rem 2rem;
		}
	}
}

.calculator__title {
	@include h3;

	color: $black_2;

	.sec_calculator & {
		@include bp($point_5) {
			font-size: 1.5rem;
			letter-spacing: 0.035em;
		}

		&:not(:last-child) {
			@include bp($point_5) {
				margin-bottom: 4px;
			}
		}
	}
}

.calculator__text {
	font-size: 1.6rem;
	line-height: 1.5;
	color: $black_3;

	.sec_calculator & {
		@include bp($point_5) {
			font-size: max(1rem, 10px);
			letter-spacing: 0.035em;
		}
	}
}

.calculator__input_w {
	max-width: 19.4rem;

	.sec_calculator & {
		@include bp($point_5) {
			max-width: 13rem;
		}
	}
}

.calculator__label {
	@include flex_block(row, nowrap, center, center, center);

	margin-bottom: 0.6rem;

	font-size: 1.6rem;
	color: $black_3;

	cursor: pointer;

	.sec_calculator & {
		@include bp($point_5) {
			margin-bottom: 4px;

			font-size: max(1rem, 10px);
			letter-spacing: 0.035em;
		}
	}
}

.calculator__label_icon {
	flex-shrink: 0;
	width: 2rem;
	height: 2rem;

	color: $black_8;

	&:not(:last-child) {
		margin-right: 4px;
	}

	.sec_calculator & {
		@include bp($point_5) {
			width: 1.2rem;
			height: 1.2rem;
		}
	}
}

.calculator__input {
	@include form_el_default;

	text-align: center;

	border-radius: $gl_radius_2;

	.sec_calculator & {
		@include bp($point_5) {
			height: 2.6rem;

			font-size: max(1rem, 10px);
		}
	}
}

.calculator__decor_w {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 0.8rem;
	height: 3.2rem;
	width: 3.2rem;

	.sec_calculator & {
		@include bp($point_5) {
			width: 2.1rem;
			height: 2.1rem;
			gap: 0.6rem;
		}
	}

	&:not(:last-child) {
		margin-bottom: 0.5rem;

		.sec_calculator & {
			@include bp($point_5) {
				margin-bottom: 0;
			}
		}
	}

	&::before,
	&::after {
		width: 2.5rem;
		height: 2px;

		background-color: $primary;
		border-radius: $gl_radius;

		content: '';

		.sec_calculator & {
			@include bp($point_5) {
				width: 1.6rem;
				height: 1px;
			}
		}
	}
}

.calculator__result_w {
	display: flex;
	flex-direction: column;
}

.calculator__result {
	@include h1;

	&::before {
		content: attr(data-calculator-currency);
	}

	.sec_calculator & {
		@include bp($point_5) {
			line-height: 1.17;
		}
	}
}

.calculator__result_text {
	font-size: 1.6rem;
	line-height: 1;
	color: $black_3;

	.sec_calculator & {
		@include bp($point_5) {
			font-size: max(1rem, 10px);
		}
	}
}

input[type='number'] {
	-moz-appearance: textfield;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}
