.about_hero {
	@include bp($point_2, min) {
		padding-top: 6.6rem;
		padding-bottom: 2rem;
	}

	@include bp($point_3) {
		padding-top: 5.6rem;
	}
}

.about_hero__content {
	position: relative;
}

.about_hero__info {
	position: absolute;
	top: -13.4rem;
	left: 0;
	z-index: -1;
	max-width: 19.8rem;

	font-family: $font_3;
	font-size: 1.4rem;
	line-height: 1.28;
	color: $black_3;

	@include bp($point_3) {
		display: none;
	}
}

.about_hero__info_decor {
	position: absolute;
	bottom: -3.4rem;
	right: 0;
	display: block;
	width: 3rem;
	height: 2.8rem;
}

.about_hero__list_w {
	background-color: $black_5;
	border-radius: 0.8rem;
	border: 1px solid $border_secondary;

	@include bp($point_2, min) {
		padding: 4.8rem 4rem 4rem;
	}

	@include bp($point_3) {
		padding: 3.6rem 3.8rem 3.2rem;
	}
}

.about_hero__list {
	display: flex;
	flex-wrap: wrap;
	margin-right: -4rem;

	@include bp($point_2, min) {
		margin-bottom: -3.4rem;
	}

	@include bp($point_3) {
		margin-bottom: -2.4rem;
	}
}

.about_hero__item {
	padding-right: 4rem;

	@include bp($point_2, min) {
		width: 25%;
		padding-bottom: 3.4rem;
	}

	@include bp($point_3) {
		width: 50%;

		padding-bottom: 2.4rem;
	}
}

.about_hero__number {
	@include h1;

	display: block;

	line-height: 1.1;
	text-align: center;
	color: $black_2;
}

.about_hero__text {
	display: block;

	font-size: 1.6rem;
	line-height: 1.5;
	text-align: center;
	color: $black_3;
}
