.hero_v2 {
	padding-top: 0;

	&.hero_v2--v2_mod {
		@include bp($point_2, min) {
			padding-bottom: 0;
		}
	}
}

.hero_v2__body {
	position: relative;
	overflow: hidden;

	background-color: $geekblue_2;

	@include bp($point_2, min) {
		border: 1px solid $geekblue_1;
		border-radius: $gl_radius;
	}

	@include bp($point_3) {
		margin: 0 calc(var(--gl-hr-indent) * -1);
	}
}

.hero_v2__row {
	position: relative;
	z-index: 1;

	min-height: 55rem;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, center, center);

		margin: 0 -2.6rem -3.2rem 0;
		padding: 3.8rem 4rem 4rem;
	}

	@include bp($point_3) {
		padding: 5.2rem 2rem 0;
	}

	.hero_v2--v1_mod & {
		position: relative;
		z-index: 1;

		@include bp($point_3) {
			padding-bottom: 4.8rem;
		}
	}

	.hero_v2--v2_mod & {
		@include bp($point_3) {
			position: relative;
		}
	}

	&:not(.hero_v2--v1_mod &) {
		@include bp($point_3) {
			border-bottom: 1px solid $geekblue_1;
		}
	}

	.hero_v2--v6_mod & {
		@include bp($point_2, min) {
			padding-top: 4.8rem;
			padding-left: 6.2rem;
			padding-right: 6.2rem;
		}
	}

	.hero_v2--v10_mod & {
		@include bp($point_2, min) {
			align-content: stretch;
			padding-top: 4.8rem;
			padding-left: 6.2rem;
			padding-right: 6.2rem;
			min-height: 51rem;
		}
	}
}

.hero_v2__column {
	@include bp($point_2, min) {
		flex-shrink: 0;
		padding: 0 2.6rem 3.2rem 0;
	}

	@include bp($point_3) {
		position: relative;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 3.4rem;
		}
	}

	&:nth-child(2n + 1) {
		@include bp($point_2, min) {
			width: 58%;
		}

		@include bp($point_3) {
			z-index: 1;
		}
	}

	&:nth-child(2n + 2) {
		@include bp($point_2, min) {
			width: 42%;
		}
	}

	.hero_v2--v3_mod & {
		&:nth-child(2n + 2) {
			@include bp($point_3) {
				max-width: 60rem;
				margin-left: auto;
				margin-right: auto;
			}
		}

		&:not(:last-child) {
			@include bp($point_3) {
				margin-bottom: 2rem;
			}
		}
	}

	.hero_v2--v4_mod &,
	.hero_v2--v5_mod &,
	.hero_v2--v7_mod {
		&:nth-child(2n + 2) {
			@include bp($point_3) {
				max-width: 60rem;
				margin-left: auto;
			}
		}
	}

	.hero_v2--v6_mod &,
	.hero_v2--v8_mod &,
	.hero_v2--v9_mod & {
		&:nth-child(2n + 2) {
			@include bp($point_3) {
				width: 100vw;
				margin-left: calc(var(--gl-hr-indent) * -1);
			}
		}
	}

	.hero_v2--v8_mod &,
	.hero_v2--v9_mod & {
		&:not(:last-child) {
			@include bp($point_3) {
				margin-bottom: -0.6rem;
			}
		}

		&:has(+ .hero_v2__column .hero_v2__video_wrap) {
			@include bp($point_3) {
				margin-bottom: 1.2rem;
			}
		}
	}

	.hero_v2--v10_mod & {
		&:nth-child(2n + 1) {
			@include bp($point_2, min) {
				width: 52.7%;
			}
		}

		&:nth-child(2n + 2) {
			@include bp($point_2, min) {
				align-self: stretch;
				width: 47.3%;
				padding-left: 3rem;
				padding-top: 2px;
				padding-bottom: 27rem;
			}
		}

		&:not(:last-child) {
			@include bp($point_3) {
				margin-bottom: 7.6rem;
			}
		}
	}
}

.hero_v2__section_decor_w {
	position: absolute;
	right: -1px;

	@include bp($point_4, min) {
		bottom: 1px;
		height: 51.8rem;
		width: 55rem;
	}

	@include bp($point_4 - 1) {
		bottom: -2px;
		height: 35.4rem;
		width: 37.5rem;
	}

	.hero_v2--v2_mod & {
		@include bp($point_4, min) {
			height: 100%;
			width: 91.8rem;
		}

		@include bp($point_4 - 1) {
			right: 0;
			bottom: 0;

			width: 100%;
			height: 78.8vw;
		}
	}
}

.hero_v2__section_decor_in {
	@include stretch;
	@include contain_img;

	object-position: bottom right;

	&.hero_v2__section_decor_in--desktop_mod {
		@include bp($point_4 - 1) {
			display: none;
		}
	}

	&.hero_v2__section_decor_in--mobile_mod {
		@include bp($point_4, min) {
			display: none;
		}
	}
}

//===LEFT COL=====================================================================================================================================================
.hero_v2__content {
	@include bp($point_2, min) {
		padding-top: 0.5rem;
	}

	@include bp($point_4, min) {
		max-width: 64.8rem;
	}

	@include bp($point_3) {
		margin: 0 auto;
	}

	.hero_v2--v9_mod & {
		@include bp($point_4, min) {
			max-width: 57.8rem;
		}
	}

	.hero_v2--v10_mod & {
		padding-top: 0;

		@include bp($point_4, min) {
			max-width: 55.6rem;
		}

		.heading .heading__title {
			&:not(:last-child) {
				@include bp($point_2, min) {
					margin-bottom: 1rem;
				}
			}
		}
	}

	.heading {
		position: relative;
		z-index: 1;

		@include bp($point_2, min) {
			text-align: left;
		}

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4rem;
			}

			@include bp($point_3) {
				margin-bottom: 3.2rem;
			}
		}
	}

	.heading__title {
		@include bp($point_3, $point_5) {
			max-width: 41.2rem;
		}

		@include bp($point_5 - 1) {
			max-width: 33.5rem;
		}

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.8rem;
			}

			@include bp($point_3) {
				margin-bottom: 0.8rem;
			}
		}
	}
}

.hero_v2__wrap {
	@include bp($point_2, min) {
		display: flex;
		gap: 2.4rem;
	}
}

.hero_v2__sellers {
	@include bp($point_2, min) {
		order: 2;
	}

	@include bp($point_3) {
		display: flex;
		justify-content: center;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 3.2rem;
		}
	}
}

.hero_v2__buttons {
	display: flex;
	gap: 1.2rem;

	@include bp($point_3) {
		justify-content: center;
	}

	.hero_v2--v1_mod &,
	.hero_v2--v2_mod & {
		a {
			@include bp($point_6 - 1) {
				width: 100%;
			}
		}
	}
}

//===RIGHT COL=====================================================================================================================================================
.hero_v2__calc {
	@include bp($point_2, min) {
		transform: translateX(4px);
	}
}

.hero_v2__picture,
.hero_v2__video_wrap {
	display: block;

	@include bp($point_2, min) {
		position: absolute;
	}

	@include bp($point_3) {
		position: relative;
	}

	.hero_v2--v2_mod & {
		@include bp($point_2, min) {
			right: 0;
			bottom: -1px;

			width: 54.8rem;
			height: 38.2rem;
		}

		@include bp($point_3) {
			width: 82%;
			padding-bottom: 73%;
			margin-left: auto;
			margin-right: calc(var(--gl-hr-indent) * -1);
		}
	}

	.hero_v2--v3_mod & {
		@include bp($point_2, min) {
			right: -4.6rem;
			top: 53.4%;
			transform: translateY(-50%);

			width: 63rem;
			height: 77.6rem;
		}

		@include bp($point_3) {
			width: 103.4%;
			padding-bottom: 70.9%;
			margin-left: -2.8%;
		}
	}

	.hero_v2--v4_mod & {
		@include bp($point_2, min) {
			right: 2.6rem;
			top: 0;

			width: 58.4rem;
			height: 51.7rem;
		}

		@include bp($point_3) {
			width: 100%;
			margin-left: var(--gl-hr-indent);
			padding-bottom: 88.5%;
		}
	}

	.hero_v2--v5_mod & {
		@include bp($point_2, min) {
			right: 2.6rem;
			bottom: 3.4rem;

			width: 57.8rem;
			height: 51.5rem;
		}

		@include bp($point_3) {
			width: 100%;
			margin-left: var(--gl-hr-indent);
			padding-bottom: 89%;
		}
	}

	.hero_v2--v6_mod & {
		@include bp($point_2, min) {
			right: 2.6rem;
			top: 0;

			width: 57.5rem;
			height: 51.6rem;
		}

		@include bp($point_3) {
			max-width: 60rem;
			margin-left: auto;

			width: 100%;
			aspect-ratio: 1 / 0.646;
		}
	}

	.hero_v2--v7_mod & {
		@include bp($point_2, min) {
			right: 2.6rem;
			top: 0;

			width: 61.6rem;
			height: 51.6rem;
		}

		@include bp($point_3) {
			width: 100%;
			margin-left: var(--gl-hr-indent);
			padding-bottom: 83.76%;
		}
	}

	.hero_v2--v8_mod &,
	.hero_v2--v9_mod & {
		@include bp($point_2, min) {
			right: 2.6rem;
			bottom: 3.4rem;

			width: 57.6rem;
			height: 51.5rem;
		}

		@include bp($point_3) {
			max-width: 60rem;
			width: 100%;
			margin-left: auto;

			aspect-ratio: 1 / 0.646;
		}
	}

	.hero_v2--v9_mod & {
		@include bp($point_2, min) {
			margin-bottom: -2px;
		}
	}

	.hero_v2--v10_mod & {
		z-index: 2;

		@include bp($point_2, min) {
			right: 1.2rem;
			bottom: 2.5rem;

			width: 45.6rem;
			aspect-ratio: 456/268;
		}

		@include bp($point_3) {
			width: calc(100% + var(--gl-hr-indent) * 2);
			margin-left: var(--gl-hr-indent);
			height: 22.2rem;
			margin-top: 4.8rem;
		}
	}
}

.hero_v2__video_wrap {
	z-index: 2;

	@include bp($point_3) {
		max-width: none !important;
	}
}

.hero_v2__img,
.hero_v2__video {
	object-position: bottom right;

	@include contain_img;

	@include bp($point_3) {
		position: absolute;
		bottom: 0;
		right: 0;
	}

	.hero_v2--v10_mod & {
		@include bp($point_3) {
			object-position: bottom center;
		}
	}
}

.hero_v2__decor_wrap {
	position: absolute;
	top: 0;

	@include bp($point_2, min) {
		right: 2.5rem;

		width: 57.6rem;
		aspect-ratio: 576/522;
	}

	@include bp($point_3) {
		right: 0;

		width: 100%;
		padding-bottom: 60%;
	}

	&.hero_v2__decor_wrap--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}

	&.hero_v2__decor_wrap--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.hero_v2__decor_in {
	@include stretch;
	@include cover_img;
}

//========================================================================================================================================================
.hero_v2__circle_decors {
	pointer-events: none;
}

.hero_v2__circle_decor {
	position: absolute;

	aspect-ratio: 1;

	background-color: rgba(0, 102, 221, 0.04);
	border-radius: 50%;

	@include bp($point_2, min) {
		right: -20.8rem;
		top: 1.2rem;

		width: 89.6rem;
	}

	@include bp($point_3) {
		left: 50%;
		bottom: max(-61vw, -32.2rem);

		width: min(160vw, 70rem);
		margin-left: max(-68.8vw, -32rem);
	}

	&.hero_v2__circle_decor--v2_mod {
		transform: scale(0.7);
	}
}

.hero_v2__heading {
	position: relative;
	z-index: 1;

	@include bp($point_2, min) {
		max-width: 31rem;
	}

	@include bp($point_3) {
		max-width: 27.4rem;
	}

	@include bp($point_3, $point_6) {
		margin-inline: auto;
	}

	.heading {
		text-align: left;
	}

	.heading__title {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 2px;
			}

			@include bp($point_3) {
				margin-bottom: 3px;
			}
		}

		h4 {
			@include bp($point_3) {
				font-size: 1.6rem;
			}
		}
	}

	.heading__text {
		@include bp($point_2, min) {
			font-size: 1.4rem;
			line-height: 1.43;
		}

		@include bp($point_3) {
			font-size: 1.3rem;
			line-height: 1.38;
		}
	}
}

.hero_v2__arrow {
	position: absolute;

	width: 3.7rem;
	height: 3.7rem;

	@include bp($point_2, min) {
		bottom: -4.6rem;
		right: 10.3rem;
	}

	@include bp($point_3) {
		bottom: -4.8rem;
		right: 13.5rem;
	}
}

.hero_v2__img_item {
	position: absolute;

	&.hero_v2__img_item--v1_mod {
		bottom: 23.8rem;
		right: 47.2%;

		@include bp($point_3) {
			display: none;
		}
	}

	&.hero_v2__img_item--v2_mod {
		@include bp($point_2, min) {
			bottom: 14.3rem;
			right: 38.2%;
		}

		@include bp($point_3) {
			bottom: 17.8rem;
			right: 75%;
		}
	}

	&.hero_v2__img_item--v3_mod {
		@include bp($point_2, min) {
			bottom: 0.7rem;
			right: 42.3%;
		}

		@include bp($point_3) {
			bottom: 2rem;
			right: 90%;
		}
	}

	&.hero_v2__img_item--v4_mod {
		@include bp($point_2, min) {
			top: -3.5rem;
			right: 26.98%;
		}

		@include bp($point_3) {
			top: -7.5rem;
			right: 75.4%;
		}
	}

	&.hero_v2__img_item--v5_mod {
		@include bp($point_2, min) {
			bottom: 33.5rem;
			right: 12.3%;
		}

		@include bp($point_3) {
			bottom: 28.2rem;
			right: max(-12.8%, -4.4rem);
		}
	}

	&.hero_v2__img_item--v6_mod {
		@include bp($point_2, min) {
			bottom: 21.6rem;
			right: 2.1%;
		}

		@include bp($point_3) {
			bottom: 18rem;
			right: -1.6%;
		}
	}

	&.hero_v2__img_item--v7_mod {
		@include bp($point_2, min) {
			bottom: 43.8rem;
			right: 5.6%;
		}

		@include bp($point_3) {
			top: -7.4rem;
			right: 3.6%;
		}
	}
}

.hero_v2__decor_img_wrap {
	display: block;
	width: 6.8rem;
	height: 6.8rem;

	border-radius: 0.8rem;
	box-shadow: 0 2px 2rem rgba($black, 0.05);
	border: 1px solid $gray_7;
	overflow: hidden;

	.hero_v2__img_item--v1_mod & {
		transform: scale(0.53);
	}

	.hero_v2__img_item--v2_mod & {
		@include bp($point_2, min) {
			transform: scale(0.82);
		}

		@include bp($point_3) {
			transform: scale(0.87);
		}
	}

	.hero_v2__img_item--v3_mod & {
		@include bp($point_3) {
			transform: scale(0.93);
		}
	}

	.hero_v2__img_item--v4_mod & {
		@include bp($point_2, min) {
			transform: scale(0.4);
		}

		@include bp($point_3) {
			transform: scale(0.49);
		}
	}

	.hero_v2__img_item--v5_mod & {
		@include bp($point_2, min) {
			transform: scale(0.92644);
		}
	}

	.hero_v2__img_item--v6_mod & {
		@include bp($point_2, min) {
			transform: scale(0.8776);
		}

		@include bp($point_3) {
			transform: scale(0.8);
		}
	}

	.hero_v2__img_item--v7_mod & {
		@include bp($point_2, min) {
			transform: scale(0.4814);
		}

		@include bp($point_3) {
			transform: scale(0.55);
		}
	}
}

.hero_v2__decor_img {
	@include contain_img;
}
