$indent: 2rem;

.key_features {
	padding: 3.2rem;

	background-color: $geekblue_2;
	border: 1px solid $geekblue_1;
	border-radius: 1.2rem;

	a {
		width: 100%;
	}
}

.key_features__title {
	@include h3;

	&:not(:last-child) {
		margin-bottom: $indent;
	}
}

.key_features__list {
	&:not(:last-child) {
		margin-bottom: $indent;
	}
}

.key_features__item {
	position: relative;
	padding-left: 3.6rem;

	color: $black;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;

		width: 2.4rem;
		height: 2.4rem;

		background-image: url(data:image/svg+xml;base64,);
		background-size: contain;
	}

	&:not(:last-child) {
		margin-bottom: $indent;
	}
}