.cta {}

.cta__card {
	position: relative;

	border: 1px solid $border_secondary;
	overflow: hidden;

	@include bp($point_2, min) {
		padding: 4rem 4rem 4.4rem;

		border-radius: 1.2rem;
	}

	@include bp($point_3) {
		padding: 4.8rem 2.4rem;

		border-radius: 0.8rem;
	}

	.cta--subscribe_mod & {
		@include bp($point_2, min) {
			padding: 4.4rem 4rem 3.8rem;
		}
	}
}

.cta__pic {
	@include stretch;
}

.cta__img {
	@include stretch;
	@include cover_img;
}

.cta__content {
	position: relative;
	z-index: 1;
}

.cta__title {
	@include h2;

	text-align: center;
	color: $black_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}

		.cta--subscribe_mod & {
			@include bp($point_2, min) {
				margin-bottom: 0.8rem;
			}
		}
	}

	h2 {
		font-weight: inherit;
	}

	strong {
		@include word_decor_2;

		font-weight: inherit;
	}
}

.cta__text {
	text-align: center;
	color: $black_3;
	font-weight: 400;

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.6rem;
		}

		.cta--subscribe_mod & {
			@include bp($point_3) {
				margin-bottom: 2.2rem;
			}
		}
	}
}

.cta__btn_w {
	display: flex;
	justify-content: center;
}

.cta__btn {
	@include bp($point_6 - 1) {
		width: 100%;
	}
}

.cta__form-w {
	max-width: 52rem;
	margin: 0 auto;

	form {
		display: flex;
		flex-wrap: wrap;
		gap: 1.2rem;
	}
}
