.article_banner {
	display: block;
	width: 100%;

	background: $geekblue_2;
	border: 1px solid $geekblue_1;
	border-radius: 0.8rem;

	text-align: center;

	@include bp($point_2, min) {
		padding: 2rem 2.8rem 2.8rem;
	}

	@include bp($point_3) {
		padding: 2rem;
	}

	a {
		width: 100%;
	}

	&:not(:last-child) {
		margin-bottom: 3.2rem;
	}

	&.article_banner--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.article_banner__row {
	@include bp($point_3) {
		display: flex;
		gap: 1.2rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}
}

.article_banner__column {
	@include bp($point_3) {
		text-align: left;
	}
}

.article_banner__img {
	display: block;

	@include bp($point_2, min) {
		width: 6.4rem;
		height: 6.4rem;
		margin-left: auto;
		margin-right: auto;
	}

	@include bp($point_3) {
		width: 3.2rem;
		height: 3.2rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.4rem;
		}
	}
}

.article_banner__title {
	@include h3;

	@include bp($point_3) {
		margin-top: 3px;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 0.6rem;
		}
	}
}

.article_banner__text {
	@include text;

	color: $black_3;
	line-height: 1.5;
}
