.card_features {
	position: relative;

	display: flex;
	width: 100%;

	background-color: $black_5;
	border: 1px solid $border_secondary;
	border-radius: $gl_radius_2;
	overflow: hidden;

	@include bp($point_2, min) {
		padding: 3.4rem 0 0 3.2rem;
	}

	@include bp($point_3) {
		min-height: 44rem;
		padding: 2.4rem 0 0 2.4rem;
	}

	.features__col--full_mod & {
		@include bp($point_2, min) {
			padding-top: 0.5rem;
			min-height: 38.3rem;
		}

		@include bp($point_3) {
			padding-top: 2.6rem;
		}
	}

	.features--v2_mod & {
		background: $geekblue_2;
		border: 1px solid $geekblue_1;

		@include bp($point_3) {
			min-height: 42.5rem;
			padding-top: 2.6rem;
		}
	}
}

.card_features__decor_w {
	position: absolute;

	.features__col--full_mod & {
		@include bp($point_2, min) {
			bottom: -4.6rem;
			left: 29.5rem;

			width: 37.6rem;
			height: 37.5rem;
		}

		@include bp($point_3) {
			bottom: 2.8rem;
			left: -1.6rem;

			width: 16rem;
			height: 16rem;
		}
	}

	&:not(.features__col--full_mod &) {
		@include bp($point_2, min) {
			bottom: -3rem;
			right: -3.2rem;

			width: 20rem;
			height: 20rem;
		}

		@include bp($point_3) {
			bottom: -2.4rem;
			right: -5.5rem;

			width: 11.8rem;
			height: 11.8rem;
		}
	}
}

.card_features__decor_in {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

.card_features__row {
	position: relative;
	z-index: 1;

	width: 100%;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, center, stretch);
	}

	@include bp($point_3) {
		display: flex;
		flex-direction: column;
	}

	&:not(.features__col--full_mod &) {
		@include bp($point_2, min) {
			flex-direction: column;
			height: 100%;
		}
	}
}

.card_features__col {
	&:not(.features__col--full_mod &) {
		@include bp($point_2, min) {
			width: 100%;
		}
	}

	.features__col--full_mod & {
		@include bp($point_2, min) {
			flex-shrink: 0;
			width: 46%;
		}
	}

	&.card_features__col--content_mod {
		&:not(:last-child) {
			@include bp($point_3) {
				margin-bottom: 2.4rem;
			}
		}

		&:not(.features__col--full_mod &, .card_features--size_2_mod &, .card_features--size_3_mod &) {
			@include bp($point_2, min) {
				flex-grow: 1;
			}
		}

		.features__col--full_mod & {
			@include bp($point_2, min) {
				padding-block: 2.4rem;
			}
		}
	}

	&.card_features__col--image_mod {
		@include bp($point_2, min) {
			@include flex_block(row, nowrap, flex-end, flex-end, flex-end);
		}

		@include bp($point_3) {
			margin-top: auto;
		}

		.features__col--full_mod & {
			margin-left: auto;
			margin-top: auto;
			align-self: flex-end;

			@include bp($point_2, min) {
				align-self: flex-end;
				width: 53.3%;
			}

			@include bp($point_3) {
				width: 100%;
			}
		}

		.card_features--size_2_mod &,
		.card_features--size_3_mod & {
			@include bp($point_2, min) {
				padding-top: 3.5rem;
				padding-bottom: 1.8rem;
			}

			@include bp($point_3) {
				padding-right: 2.4rem;
			}
		}

		.card_features--size_2_mod & {
			@include bp($point_3) {
				padding-bottom: 2rem;
			}
		}

		&:not(.features__col--full_mod &, .card_features--size_2_mod &, .card_features--size_3_mod &) {
			@include bp($point_3) {
				margin-left: -2.4rem;
			}

			.features--v2_mod & {
				@include bp($point_2, min) {
					width: calc(100% + 3.2rem);
					margin-left: -3.2rem;
				}

				@include bp($point_3) {
					width: calc(100% + 2.4rem);
					margin-left: -2.4rem;
				}
			}
		}

		.card_features--size_3_mod & {
			@include bp($point_2, min) {
				margin-left: -3px;
			}

			@include bp($point_3) {
				margin-left: -0.5rem;
				margin-right: -0.3rem;
				margin-top: -3px;
			}
		}
	}

	.features--v2_mod & {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 2.2rem;
			}

			@include bp($point_3) {
				margin-bottom: 1.6rem;
			}
		}
	}
}

.card_features__heading {
	@include bp($point_2, min) {
		padding-right: 4.6rem;
	}

	@include bp($point_3) {
		padding-right: 2.2rem;
	}

	.features--v2_mod & {
		@include bp($point_3) {
			padding-right: 1.2rem;
		}
	}
}

.card_features__title {
	@include h3;

	line-height: 1.33;

	&:not(:last-child) {
		margin-bottom: 2px;
	}

	.features--v2_mod & {
		&:not(:last-child) {
			margin-bottom: 0.8rem;
		}
	}
}

.card_features__text {
	font-size: 1.6rem;
	line-height: 1.5;
	color: $black_2;

	&:not(.features--v2_mod &) {
		color: $black_3;
	}

	h3 {
		font-weight: inherit;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.7rem;
		}
	}

	ul {
	}

	ul li {
		position: relative;

		padding-left: 2.6rem;

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}

		&::before {
			position: absolute;
			top: 2px;
			left: 0;

			width: 2rem;
			height: 2rem;

			background: url(data:image/svg+xml;base64,)
				center / contain no-repeat;

			content: '';
		}
	}
}

.card_features__img_w {
	position: relative;

	display: block;
	width: 100%;
	aspect-ratio: 586/301;

	.card_features--size_1_mod & {
		@include bp($point_2, min) {
			aspect-ratio: 599/316;
		}

		@include bp($point_3) {
			aspect-ratio: 308/195;
		}
	}

	.card_features--size_2_mod & {
		max-width: 48.4rem;
		aspect-ratio: 484/200;
		margin-right: auto;
	}

	.card_features--size_3_mod & {
		max-width: 53.8rem;
		aspect-ratio: 528/220;
		margin-right: auto;
	}

	.features__col--full_mod & {
		@include bp($point_3) {
			aspect-ratio: 335/203;
			margin-left: -2.5rem;
			margin-right: -1px;
			width: calc(100% + 2.6rem);
		}
	}

	.features--v2_mod & {
		aspect-ratio: 403/272;
	}
}

.card_features__img_in {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	object-fit: contain;
	object-position: right bottom;

	.card_features--size_2_mod & {
		border-radius: $gl_radius_2;
		box-shadow: 0 2px 1.8rem rgba($black, 0.05);
	}
}
