.stores_portfolio__body {
	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2.4rem;
		}
	}
}

.stores_portfolio__list {
	@include bp($point_4, min) {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -2.4rem -2.4rem 0;
	}
}

.stores_portfolio__item {

	@include bp($point_4, min) {
		padding: 0 2.4rem 2.4rem 0;
		width: 50%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			margin-bottom: 1.6rem;
		}
	}
}

.stores_portfolio__card {
	border: 1px solid $border_secondary;
	background: rgba($black, 0.02);
	overflow: hidden;

	@include bp($point_2, min) {
		padding: 8rem 2.6rem;
		border-radius: 1.2rem;
	}

	@include bp($point_3) {
		padding: 4.8rem 2.4rem;
		border-radius: .8rem;
	}
}

.stores_portfolio__picture {
	position: relative;
	display: block;
	width: 100%;
	padding-bottom: 60%;
}

.stores_portfolio__img {
	@include contain_img;

	position: absolute;
	top: 0;
	left: 0;
}

.stores_portfolio__footer {
	text-align: center;
}