.feature_card {
	position: relative;
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;

	border: 1px solid $border_secondary;
	background-color: $black_5;
	overflow: hidden;

	@include bp($point_2, min) {
		padding: 3.2rem 3rem 3.2rem 3.2rem;

		border-radius: 1.2rem;
	}

	@include bp($point_3) {
		padding: 2.4rem;

		border-radius: 0.8rem;
	}

	.values--v2_mod & {
		@include bp($point_3) {
			padding: 3.2rem;
		}
	}

	&.feature_card--no_bg_mod {
		background-color: transparent;
		border-color: transparent;
	}

	&.feature_card--center_mod {
		align-items: center;
	}
}

.feature_card__decor {
	position: absolute;
	top: 0;
	right: 0;

	display: block;
	width: 11.2rem;
	height: 11.2rem;
}

.feature_card__icon {
	&:not(:last-child) {
		margin-bottom: 2.6rem;
	}
}

.feature_card__title {
	@include h3;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2px;
		}

		@include bp($point_3) {
			margin-bottom: 3px;
		}
	}

	.feature_card--center_mod & {
		text-align: center;
	}

	.feature_card--text_sm_mod & {
		font-size: 2rem;
	}
}

.feature_card__text {
	@include text;

	line-height: 1.5;
	color: $black_3;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}

	p {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.4rem;
			}

			@include bp($point_3) {
				margin-bottom: 1rem;
			}
		}
	}

	.feature_card--center_mod & {
		text-align: center;
	}
}