.sellers {
	display: flex;
	align-items: center;
	gap: 1.2rem;
}

.sellers__list {
	display: flex;
}

.sellers__item {
	width: 4rem;
	height: 4rem;
	border-radius: 50%;
	overflow: hidden;

	&:not(:last-child) {
		margin-right: -2rem;
	}

	&.sellers__item--number_mod {
		display: flex;
		align-items: center;
		justify-content: center;

		box-shadow: 0 2px 1.8rem 0 rgba($black, 0.05);
		background: $white;
		border: 1px solid $gray_7;
	}
}

.sellers__picture {
	display: block;
	width: 100%;
	height: 100%;
}

.sellers__img {
	@include cover_img;
}

.sellers__number {
	margin-left: -3px;

	font-weight: 600;
	font-size: 1.2rem;
	text-align: center;
	color: $black_2;
}

.sellers__label {
	font-size: 1.4rem;
	line-height: 1.57;
	color: $black_3;

	.hero__sellers & {
		@include text;
	}
}