.kickstart__list_wrap {
	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 1.6rem;
		}
	}
}

.kickstart__list {
	display: flex;
	flex-wrap: wrap;

	@include bp($point_4, min) {
		margin: 0 -2.4rem -2.4rem 0;
	}
}

.kickstart__list_item {
	@include bp($point_2, min) {
		width: 25%;
		padding: 0 2.4rem 2.4rem 0;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
		padding: 0 2.4rem 2.4rem 0;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			margin-bottom: 1.6rem;
		}
	}
}

.kickstart__card {
	position: relative;
	height: 100%;
	display: flex;
	flex-direction: column;
	width: 100%;

	background-color: rgba($black, 0.02);
	border: 1px solid $border_secondary;
	border-radius: 1.2rem;

	overflow: hidden;

	.kickstart__list_item:first-child & {
		background-color: $geekblue_2;
		border-color: $geekblue_1;
	}

	@include bp($point_4, min) {
		min-height: 29.8rem;
		padding: 2.6rem 2.4rem 1.8rem;
	}

	@include bp($point_4 - 1) {
		padding: 2rem 3.2rem 2.4rem 3rem;
	}
}

.kickstart__card_head {
	display: flex;
	align-items: center;

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}

.kickstart__card_head_in {
	@include bp($point_4, min) {
		padding-top: 0.4rem;
		display: flex;
		align-items: center;
		width: 100%;
	}

	@include bp($point_4 - 1) {
		margin-left: auto;

		text-align: right;
	}

	.kickstart__card_head:not(:last-child) & {
		@include bp($point_4, min) {
			margin-bottom: 1rem;
		}
	}
}

.kickstart__card_img {
	display: block;

	img {
		z-index: 3;
		position: relative;
	}

	.kickstart__list_item:nth-child(1) & {
		@include bp($point_4 - 1) {
			width: 6.4rem;
			height: 6.4rem;
			padding: 1.2rem;
			margin-left: 0.2rem;

			border: 1px solid #ececec;
			border-radius: 0.8rem;
			background-color: $white;
			filter: drop-shadow(0px 2px 18px rgba(#1677ff, 0.05));

			&:not(:last-child) {
				margin-bottom: 1.8rem;
			}
		}
	}

	@include bp($point_4, min) {
		position: absolute;
		bottom: 0;
		right: 0;

		width: 15rem;
		height: 15rem;
		margin: 0 -5rem -5rem 0;
	}

	@include bp($point_4 - 1) {
		width: 7.2rem;
		height: 7.2rem;
		margin-left: -0.4rem;

		margin-right: 2rem;
	}

	&:before,
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 2;

		border-radius: 50%;

		@include bp($point_4 - 1) {
			display: none;
		}

		.kickstart__list_item:nth-child(1) & {
			background-color: #e3e6fc;
		}
		.kickstart__list_item:nth-child(2) & {
			background-color: $red_2;
		}

		.kickstart__list_item:nth-child(3) & {
			background-color: #9000dd;
		}

		.kickstart__list_item:nth-child(4) & {
			background-color: $blue_4;
		}
	}

	.kickstart__list_item:nth-child(1) & {
		&:before {
			opacity: 0.11;
			background-color: #c8d0f9;
		}

		&:after {
			opacity: 0.24;
		}
	}

	&:before {
		width: 21.2rem;
		height: 21.2rem;
		margin: -7.6rem 0 0 -9.6rem;

		opacity: 0.04;
	}

	&:after {
		width: 30.4rem;
		height: 30.4rem;
		margin: -12.2rem 0 0 -14.2rem;

		opacity: 0.02;
	}
}

.kickstart__card_img_in {
	@include contain_img;

	.kickstart__list_item:nth-child(3n + 1) & {
		@include bp($point_4, min) {
			margin: -3.5rem 0 0 -1rem;
		}
	}

	.kickstart__list_item:nth-child(3n + 2) & {
		@include bp($point_4, min) {
			margin-top: -2rem;
			margin-left: -2.2rem;
		}
	}

	.kickstart__list_item:nth-child(3n + 3) & {
		@include bp($point_4, min) {
			margin-top: -2rem;
			margin-left: -3.6rem;
		}
	}
	.kickstart__list_item:nth-child(3n + 4) & {
		@include bp($point_4, min) {
			margin: -2.2rem 0 0 -3.3rem;
		}
	}
}

.kickstart__card_label {
	font-size: 1.6rem;
	font-weight: 600;
	font-family: $font_2;

	.kickstart__list_item:nth-child(3n + 2) & {
		background: -webkit-linear-gradient(#ffa940, #db7900);
	}

	.kickstart__list_item:nth-child(3n + 3) & {
		background: -webkit-linear-gradient(#ef4dba, #ff3883);
	}
	.kickstart__list_item:nth-child(3n + 4) & {
		background: -webkit-linear-gradient(#5863ef, #1a25b7);
	}

	-webkit-background-clip: text !important;
	-webkit-text-fill-color: transparent !important;

	@include bp($point_4 - 1) {
		margin-bottom: 0.4rem;
	}
}

.kickstart__card_price {
	display: flex;
	align-items: center;
	gap: 0.8rem;
	margin-left: auto;
}

.kickstart__card_price_value {
	font-weight: 600;
	font-family: $font_2;
	line-height: 1.2;

	&.kickstart__card_price_value--old_mod {
		font-size: 1.6rem;
		color: $black;
		opacity: 0.45;
		text-decoration: line-through;

		@include bp($point_4, min) {
			margin-top: 0.2rem;
		}
	}

	&.kickstart__card_price_value--new_mod {
		font-size: 2rem;
		color: $green;
		text-transform: uppercase;
	}
}

.kickstart__card__title {
	font-size: 2rem;
	font-family: $font_2;
	font-weight: 600;

	opacity: 0.88;

	@include bp($point_4 - 1) {
		padding-right: 1rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 0.4rem;
		}
	}

	&.kickstart__card__title--size_mod {
		font-size: 2.4rem;
		line-height: 1.35;
	}
}

.kickstart__card__text {
	font-size: 1.6rem;
	opacity: 0.65;

	@include bp($point_4, min) {
	}
	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

// gif block

.kickstart__video {
	position: relative;

	border-radius: 1.2rem;
	overflow: hidden;

	@include bp($point_2, min) {
		height: 100%;
	}

	@include bp($point_3) {
		aspect-ratio: 1;
	}
}

.kickstart__video_img_in {
	position: absolute;
	top: 0;
	left: 0;

	@include cover_img;
}

// banner

.kickstart__banner {
	position: relative;

	overflow: hidden;

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: -2;

		width: 100%;
		height: 100%;

		background-color: $geekblue_2;
		border: 1px solid $geekblue_1;
		border-radius: 1.2rem;
	}

	@include bp($point_2, min) {
		display: flex;
		align-items: center;
		padding: 2rem 2.4rem;
	}

	@include bp($point_3) {
		padding: 2.2rem 3.2rem;
	}
}

.kickstart__banner_img {
	width: 6.4rem;
	height: 6.4rem;
	padding: 1.2rem 1.2rem;
	flex-shrink: 0;

	background-color: $white;
	border: 1px solid $gray_7;

	border-radius: 0.8rem;

	@include bp($point_2, min) {
		margin-right: 2.2rem;
	}

	@include bp($point_3) {
		margin-bottom: 2.2rem;
	}
}

.kickstart__banner_img_in {
	@include contain_img;
}

.kickstart__banner_content {
	@include bp($point_2, min) {
		margin-right: 2rem;
	}

	@include bp($point_3) {
		margin-bottom: 2.4rem;
	}
}

.kickstart__banner_title {
	font-size: 2.4rem;
	font-weight: 600;
	font-family: $font_2;
	line-height: 1.33;
}

.kickstart__banner_text {
	font-size: 1.6rem;
	line-height: 1.5;
}

.kickstart__banner_price {
	@include bp($point_2, min) {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		margin-left: auto;
	}

	@include bp($point_3) {
		margin-bottom: 2rem;
	}

	&:not(:last-child) {
		margin-right: 4rem;
	}
}

.kickstart__banner_price_main {
	font-family: $font_2;
	font-weight: 600;
	line-height: 1.2;
	color: $primary;

	@include bp($point_2, min) {
		font-size: 3.6rem;
	}

	@include bp($point_3) {
		font-size: 3.2rem;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 0.6rem;
		}
	}
}

.kickstart__banner_price_info {
	display: flex;
	gap: 0.8rem;
}

.kickstart__banner_price_title {
	line-height: 1.4;
	font-weight: 500;
	opacity: 0.45;

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}

.kickstart__banner_price_value {
	line-height: 1.4;
	font-family: $font_2;
	font-weight: 600;
	text-decoration: line-through;

	opacity: 0.65;

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}

.kickstart__banner_btn {
	a {
		@include bp($point_3) {
			width: 100%;
		}
	}
}

.kickstart__banner_decor {
	position: absolute;
	top: calc(100% - 14.6rem);
	left: 50%;
	z-index: -1;

	width: 42rem;
	height: 42rem;
	margin-left: -21rem;

	border-radius: 50%;
	background-color: #e3ecfc;
	opacity: 0.4;

	&:before,
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;

		transform: translate(-50%, -50%);
		border-radius: 50%;
	}

	&:before {
		width: 29.2rem;
		height: 29.2rem;
		background-color: #c8d9f9;
	}

	&:after {
		width: 17rem;
		height: 17rem;
		background-color: #acc1f6;
	}

	@include bp($point_2, min) {
		display: none;
	}
}

.kickstart__btn_wrap {
	display: flex;
	align-items: center;
	justify-content: center;

	@include bp($point_4 - 1) {
		justify-content: stretch;
		display: grid;
	}
}

.kickstart__card__bottom {
	margin-top: auto;
}

.kickstart__card__bottom_subtitle {
	font-size: 1.6rem;
	color: rgba($black, 0.45);
	//white-space: break-spaces;

	strong {
		font-weight: 600;
	}

	&:not(:last-child) {
		margin-bottom: 2px;
	}
}

.kickstart__card__bottom_title {
	color: $primary;
	font-size: 3rem;
	line-height: 1.2;
	font-weight: 600;
}
