.pagination {
	display: flex;
	justify-content: center;
}

.pagination__list,ul.page-numbers {
	display: flex;
	@include bp($point_2, min) {
		margin-right: -0.8rem;
	}

	@include bp($point_3) {
		margin-right: -1rem;
	}
}

.pagination__item,.page-numbers li {
	@include bp($point_2, min) {
		padding-right: 0.8rem;
	}

	@include bp($point_3) {
		padding-right: 1rem;
	}
}

.pagination__link,a.page-numbers,span.page-numbers {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 3.2rem;
	height: 3.2rem;

	font-size: 1.4rem;
	color: $black_2;

	border: 1px solid transparent;
	border-radius: 0.6rem;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	.pagination__item--active_mod &,
	&:hover{
		background: $black_7;
	}

	.pagination__item--active_mod &{
		font-weight: 600;
		color: $primary;

		pointer-events: none;
		border-color: $primary;
		background-color: $white;
	}

	.pagination__item--dots_mod & {
		border-color: transparent;

		&:hover {
			color: $black;
			background: $white;
		}
	}

	.pagination__item--disabled_mod & {
		pointer-events: none;
	}
}

span.page-numbers.current{
	font-weight: 600;
	color: $primary;

	pointer-events: none;
	border-color: $primary;
	background-color: $white;
}

.pagination__link_icon {
	display: block;
	width: 1.4rem;
	height: 1.4rem;

	color: $black_2;

	.pagination__item--disabled_mod & {
		color: $black_6;
	}

	.pagination__item--next_mod & {
		transform: rotate(180deg);
	}
}
