.contact {
	@include bp($point_2, min) {
		padding-top: 7rem;
	}

	@include bp($point_3) {
		padding-top: 5.4rem;
	}

	.section_in {
		max-width: 105rem;
	}
}

.contact__card {
	border-radius: 0.8rem;
	border: 1px solid $geekblue_1;
	background-color: $geekblue_2;

	@include bp($point_2, min) {
		display: flex;
		align-items: baseline;
		padding: 3.2rem;
	}

	@include bp($point_3) {
		padding: 2.6rem 2rem 2.8rem;
	}
}

.contact__form {
	@include bp($point_2, min) {
		width: 54.4%;
		padding-right: 3.8rem;

		border-right: 1px solid $black_4;
	}

	@include bp($point_3) {
		padding-bottom: 2.4rem;

		border-bottom: 1px solid $black_4;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 2.6rem;
		}
	}
}

.contact__form_title {
	@include h3;

	color: $black_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;

			text-align: center;
		}
	}
}

.contact__info {
	@include bp($point_2, min) {
		width: 45.6%;
		padding-left: 3.8rem;
	}
}

.contact__info_title {
	@include h4;

	color: $black_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.4rem;

			text-align: center;
		}
	}
}

.contact__info_item {
	display: flex;

	@include bp($point_2, min) {
		gap: 1.8rem;
	}

	@include bp($point_3) {
		gap: 1.4rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.2rem;
		}
	}
}

.contact__info_content {
	font-family: $font_1;
}

.contact__info_category {
	font-size: 1.4rem;
	font-weight: 600;

	.contact__info_item--color_0_mod & {
		color: $purple_5;
	}

	.contact__info_item--color_1_mod & {
		color: $cyan;
	}

	.contact__info_item--color_2_mod & {
		color: $orange;
	}

	&:not(:last-child) {
		margin-bottom: 0.6rem;
	}
}

.contact__info_name {
	@include h5;

	font-family: $font_1;
	font-weight: 600;

	color: $black_2;

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 2px;
		}
	}
}

.contact__info_link {
	@include text;

	color: $black_2;
}
