.ideas_card {
	background-color: $black_9;
	border: 1px solid $border_secondary;
	border-radius: 0.8rem;
}

.ideas_card__top {
	display: flex;
	flex-direction: column;
	gap: 1.6rem;
	padding: 2rem 2rem 2.4rem;
}

.ideas_card__img_wrap {
	display: block;
	max-width: 14rem;
	aspect-ratio: 1;
	margin-inline: auto;

	&:not(:last-child) {
		margin-bottom: 4px;
	}
}

.ideas_card__img {
	@include contain_img;
}

.ideas_card__title {
	font-family: $font_2;
	font-size: 1.8rem;
	font-weight: 600;
	line-height: 1.33;
	color: $black_2;
	text-align: center;

	>* {
		font-weight: inherit;
	}
}

.ideas_card__btn_wrap {
	a {
		width: 100%;
	}
}

//===Bottom=====================================================================================================================================================
.ideas_card__popularity {
	padding: 0.4rem 1.2rem 0.6rem 2rem;

	border-top: 1px solid $black_4;

	@include flex_block(row, nowrap, flex-start, center, center);

	@include bp($point_2, min) {
		gap: 1rem;
	}

	@include bp($point_3) {
		gap: 4rem;
	}
}

.ideas_card__col {
	&:nth-child(2) {
		@include bp($point_2, min) {
			margin-inline-start: auto;
		}

		@include bp($point_3) {
			margin-inline: auto;
		}
	}
}

.ideas_card__label {
	font-size: 1rem;
	font-weight: 500;
	line-height: 1.6;
	text-transform: uppercase;
	color: $black_8;

	&:not(:last-child) {
		margin-bottom: 1px;
	}
}

.ideas_card__value {
	font-size: 1.5rem;
	font-weight: 500;
	line-height: 1.33;
	color: $green;

	.ideas_card--rare_mod & {
		color: $green_4;
	}

	.ideas_card--medium_mod & {
		color: $orange_2;
	}

	.ideas_card--popular_mod & {
		color: $orange_3;
	}

	.ideas_card--mainstream_mod & {
		color: $red_3;
	}
}

.ideas_card__popularity_img_wrap {
	width: 10.4rem;
	aspect-ratio: 1.791;
}

.ideas_card__popularity_img {
	@include contain_img;
}
