@use 'sass:math';
$transition: .3s ease-in-out;

.hero_product {
	position: relative;

	display: block;
	width: 100%;
	height: 100%;
	border-radius: 50%;

	background-color: $white;
	box-shadow: 0 2px 2em 0 rgba($black, .03);
	border: 1px solid transparent;

	transition: border-color $transition, transform $transition;
	transform: scale(.88);

	.hero__card--active_mod & {
		border-color: rgba($blue, .78);
		transform: scale(1);
	}

	.hero__card[data-inactive] & {
		filter: blur(3px);
	}
}

.hero_product__picture {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
	border-radius: 50%;
	overflow: hidden;
}

.hero_product__img {
	@include contain_img;
}

.hero_product__item {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 4;

	display: flex;
	gap: .6em;

	width: 12.5em;
	padding: .6em;

	border-radius: 9.5em;
	border: 1px solid #EFEFEF;
	box-shadow: 0 2px 1.1em 0 $black_4;
	background-color: $white;

	opacity: 0;
	pointer-events: none;

	transition: opacity $transition;

	.hero__card--active_mod & {
		opacity: 1;
		pointer-events: initial;
	}

	&.hero_product__item--v0_mod {
		.hero__card--v0_mod & {
			margin: -68% 0 0 0;
		}

		.hero__card--v1_mod & {
			margin: -68% 0 0 -14%;
		}

		.hero__card--v2_mod & {
			margin: -100% 0 0 -14%;
		}

		.hero__card--v3_mod & {
			margin: -82% 0 0 -14%;
		}

		.hero__card--v4_mod & {
			margin: -74% 0 0 -50%;
		}

		.hero__card--v5_mod & {
			margin: -110% 0 0 -8%;
		}
	}

	&.hero_product__item--v1_mod {
		.hero__card--v0_mod & {
			margin: 34% 0 0 21%;
		}

		.hero__card--v1_mod & {
			margin: 46% 0 0 -13%;
		}

		.hero__card--v2_mod & {
			margin: 30% 0 0 12%;
		}

		.hero__card--v3_mod & {
			margin: -32% 0 0 43%;
		}

		.hero__card--v4_mod & {
			margin: -40% 0 0 -108%;
		}

		.hero__card--v5_mod & {
			margin: -20% 0 0 -250%;
		}
	}

	&.hero_product__item--v2_mod {
		.hero__card--v0_mod & {
			margin: 34% 0 0 -104%;
		}

		.hero__card--v1_mod & {
			margin: -8% 0 0 39%;
		}

		.hero__card--v2_mod & {
			margin: 30% 0 0 -186%;
		}

		.hero__card--v3_mod & {
			margin: 19% 0 0 23%;
		}

		.hero__card--v4_mod & {
			margin: 0% 0 0 -118%;
		}

		.hero__card--v5_mod & {
			margin: 44% 0 0 -35%;
		}
	}
}

.hero_product__wrap {
	display: flex;
	align-items: center;
	justify-content: center;

	width: 3em;
	height: 3em;
	border-radius: 50%;

	.hero_product__item--v0_mod & {
		background-color: rgba(#C5E0FF, .71);
	}

	.hero_product__item--v1_mod & {
		background-color: rgba(#DDF9E8, .83);
	}

	.hero_product__item--v2_mod & {
		background-color: rgba(#AF62EC, .18);
	}
}

.hero_product__icon {
	display: block;
	flex-shrink: 0;

	width: 1.8em;
	height: 1.8em;

	.hero_product__item--v0_mod & {
		color: #006CEB;
	}

	.hero_product__item--v1_mod & {
		color: #00CD6B;
	}

	.hero_product__item--v2_mod & {
		color: #A22DFF;
	}
}

.hero_product__content {}

.hero_product__label {
	font-size: 1.1em;
	line-height: 1;
	color: #A1A1A1;

	&:not(:last-child) {
		margin-bottom: math.div(.5em, 1.1);
	}
}

.hero_product__price {
	font-size: 1.3em;
	font-weight: 700;
	line-height: 1;
	color: #252f43;
}