@mixin form_el_checkbox {
	position: relative;

	padding-left: 3rem;

	font-size: 1.6rem;
	color: $black;
	cursor: pointer;

	&:before,
	&:after {
		position: absolute;
		top: -1px;
		left: 0;
		width: 2rem;
		height: 2rem;
		cursor: pointer;
		content: '';
	}

	&:before {
		border-radius: 2px;
		border: 1px solid $black;

		transition: transform 0.2s, opacity 0.2s;
		will-change: transform;

		.form_checkbox__element:checked + & {
			opacity: 0;
			transform: rotateZ(40deg) scaleX(0.6) translateY(-3px);
		}
	}

	&::after {
		opacity: 0;

		border-bottom: 2px solid $black;
		border-right: 3px solid $black;

		transition: transform 0.2s, opacity 0.2s;

		.form_checkbox__element:checked + & {
			transform: rotateZ(40deg) scaleX(0.6) translateY(-3px);
			opacity: 1;
		}
	}

	//--------------------------------------------- Font icon version
	// &:after {
	// 	@include icon-check;
	// 	@include transition_opacity;

	// 	text-align: center;
	// 	line-height: 2rem;
	// 	opacity: 0;
	// 	color: $black;

	// 	.form_checkbox__element:checked + & {
	// 		opacity: 1;
	// 	}
	// }
}

@mixin form_el_checkbox_2 {
	position: relative;
	display: block;
	width: 100%;
	padding: 0.5rem 3.2rem 0.5rem 1.2rem;

	font-size: 1.4rem;
	font-weight: 400;
	color: $black_2;

	border-radius: 4px;
	cursor: pointer;

	.form_checkbox__element:checked + & {
		font-weight: 600;
		background-color: $color_info_bg;
	}

	&:hover,
	&:focus {
		background-color: $gray_8;
	}

	&:after {
		position: absolute;
		content: '';
		z-index: 1;
		top: 0.4rem;
		right: 1rem;
		display: block;
		width: 2rem;
		height: 2rem;

		background-image: url('data:image/svg+xml;base64,');

		opacity: 0;

		.form_checkbox__element:checked + & {
			opacity: 1;
		}
	}
}
