.testimonials_v2__list_w {
	position: relative;
	will-change: height;

	overflow: hidden;
	will-change: max-height;
	transition: max-height 0.3s ease-in-out;

	@include bp($point_4, min) {
		max-height: 52.2rem;
	}

	@include bp($point_4 - 1) {
		max-height: 110rem;
	}

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 4.6rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2.4rem;
		}
	}

	&::after {
		position: absolute;
		content: '';
		bottom: 0;
		left: 0;
		width: 100%;
		height: 32.2rem;
		max-height: 100%;

		background: linear-gradient(180deg, rgba(249, 249, 250, 0) 67.88%, $white 100%);
		transition: opacity 0.3s ease;
		pointer-events: none;
	}

	&.testimonials__list_w--open_state::after {
		opacity: 0;
	}
}

.testimonials_v2__list {
	column-gap: 0;

	@include bp($point_2, min) {
		column-count: 3;
	}

	@include bp($point_3, $point_4) {
		column-count: 2;
	}

	@include bp($point_4, min) {
		margin-right: -2.4rem;
		margin-bottom: -2rem;
	}
}

.testimonials_v2__item {
	display: inline-flex;
	width: 100%;

	@include bp($point_4, min) {
		padding-right: 2.4rem;
		padding-bottom: 2rem;
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}
}

.testimonials_v2__btn_w {
	display: flex;
	justify-content: center;
}
