.stars {
	display: flex;
	align-items: center;
	gap: .4rem;
}

.stars__item {
	position: relative;

	width: 2rem;
	height: 2rem;
}

.stars__in {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	display: block;
	width: 1.6rem;
	height: 1.6rem;

	&.stars__in--unfilled_mod {
		z-index: 1;
		color: rgba($black, .15);
	}

	&.stars__in--filled_mod {
		z-index: 2;
		color: $primary;
	}
}