@import 'ScssComponents/social.scss';

.footer {
	width: 100%;
	min-height: var(--height-footer);
	flex: 0 0 auto;

	font-size: 1rem;
	padding: 0 var(--gl-hr-indent);

	background-color: $geekblue_2;

	overflow: hidden;
	will-change: transform;
}

.footer__body {
	padding: 4.8rem 0 4.8rem;

	@include bp($point_3) {
		padding-bottom: 2.4rem;
	}
}

.footer__row {
	@include flex_block(row, nowrap, flex-start);

	@include bp($point_2, min) {
		margin: 0 -3.6rem -3.6rem 0;
	}

	@include bp($point_3) {
		flex-direction: column;
	}
}

.footer__col {
	@include bp($point_2, min) {
		padding: 0 3.6rem 3.6rem 0;
	}

	@include bp($point_3) {
		width: 100%;
	}

	&.footer__col--info_mod {
		@include bp($point_2, min) {
			flex-grow: 1;
		}

		@include bp($point_3) {
			order: 2;
		}
	}

	&.footer__col--nav_mod {
		@include bp($point_2, min) {
			flex-shrink: 0;
			width: 58.6%;
		}

		@include bp($point_3) {
			order: 1;

			margin-bottom: 0.8rem;
		}
	}
}

.footer__info_content {
	max-width: 36.5rem;

	font-size: 1.4rem;
	line-height: 1.57;
}

.footer__info_top {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.5rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.5rem;
		}
	}
}

.footer__info_bottom {
}

.footer__logo {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 0.8rem;
		}
	}

	&.footer__logo--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}

	&.footer__logo--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.footer__link_logo {
	display: inline-block;
}

.footer__logo_img_w {
	display: block;
	width: 12.8rem;
	height: 2.4rem;
	margin-left: 3px;
}

.footer__logo_img_in {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
}

.footer__text {
	color: rgba($black, 0.65);

	&.footer__text--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}

		@include bp($point_3) {
			max-width: 36.5rem;

			font-size: 1.4rem;
			line-height: 1.57;
		}
	}

	&.footer__text--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 2.2rem;
		}
	}
}

.footer__social {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.footer__status {
	@include flex_block(row, nowrap, flex-start, center, center);

	gap: 0.8rem;
	padding-left: 0.6rem;

	color: rgba($black, 0.88);
	transition: color 0.3s ease 0s;

	&::before {
		flex-shrink: 0;
		flex-basis: 1rem;
		width: 1rem;
		height: 1rem;

		background-color: $green_2;
		border-radius: 50%;

		content: '';
	}

	&.footer__status--offline_mod {
		&::before {
			background-color: $red;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;
		color: $primary;
	}
}

.footer__text_status {
	color: inherit;
}

//===NAV=====================================================================================================================================================
.footer__nav {
	@include flex_block(row, wrap, flex-start);

	@include bp($point_2, min) {
		padding-right: 7.6rem;
		padding-left: 4.5rem;
		flex-wrap: nowrap;
	}

	@include bp($point_6, min) {
		margin: 0 -4.5rem -4.5rem 0;
	}

	@include bp($point_6 - 1) {
		margin: 0 0 -2.4rem 0;
	}
}

.footer__col_nav {
	flex-shrink: 0;
	max-width: 25.8rem;

	@include bp($point_6, min) {
		padding: 0 4.5rem 4.5rem 0;
	}

	@include bp($point_2, min) {
		margin-left: auto;
	}

	@include bp($point_3, $point_6) {
		width: 33.333%;
		min-width: 22.5rem;
		flex-grow: 1;
	}

	@include bp($point_6 - 1) {
		width: 50%;
		padding: 0 2.4rem 2.4rem 0;
	}
}

.footer__title {
	@include h4;

	line-height: 1.4;
	color: rgba($black, 0.88);

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.footer__nav_item {
	&:not(:last-child) {
		margin-bottom: 1.2rem;
	}
}

.footer__nav_link {
	font-size: 1.6rem;
	line-height: 1.5;
	color: rgba($black, 0.88);

	transition: color 0.2s ease;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $primary;
	}
}

.footer__bottom {
	position: relative;

	display: flex;
	gap: 1.2rem 2rem;
	padding: 2.4rem 0;

	font-size: 1.6rem;
	color: $black_3;
	line-height: 1.57;

	@include bp($point_2, min) {
		align-items: center;
	}

	@include bp($point_3) {
		flex-direction: column;
		row-gap: 1.6rem;
	}

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;

		width: 100vw;
		height: 1px;

		background: #c0c9d5;
		pointer-events: none;
	}
}

.footer__copy {
	@include bp($point_2, min) {
		flex-shrink: 0;
	}

	@include bp($point_3) {
		order: 2;
	}
}

.footer__legal_list {
	@include bp($point_2, min) {
		gap: 1.2rem;

		@include flex_block(row, wrap, flex-start, center, center);
	}

	@include bp($point_3) {
		order: 1;
	}
}

.footer__legal_item {
	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 1.1rem;
		}
	}
}

.footer__legal_link {
	color: $black_2;

	transition: color 0.2s ease;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $primary;
	}
}

.footer__label {
	@include bp($point_2, min) {
		margin-left: auto;
		flex-shrink: 0;
	}

	@include bp($point_3) {
		order: 3;
	}
}

.footer__icon {
	display: inline-block;
	width: 2.4rem;
	height: 2.4rem;

	vertical-align: middle;
	margin: -4px 4px 0 1px;
}
