.source {
	position: relative;
	width: 100%;
	height: 100%;

	border: 1px solid $border_secondary;
	background-color: $gray_11;
	overflow: hidden;

	@include bp($point_2, min) {
		padding: 3.2rem 6rem 3.2rem 3.2rem;

		border-radius: 1.2rem;
	}

	@include bp($point_3) {
		padding: 2.4rem;

		border-radius: 0.8rem;
	}

	&.source--lg_mod {
		background-color: $geekblue_2;
		border-color: $geekblue_1;
	}
}

.source_pic {
	@include stretch;

	@include bp($point_4 - 1) {
		display: none;
	}
}

.source_img {
	@include stretch;
	@include cover_img;

	object-position: top left;
}

.source__content {
	position: relative;
	z-index: 1;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 100%;
	height: 100%;

	.source--lg_mod & {
		@include bp($point_4, min) {
			max-width: 44%;
		}
	}
}

.source__icon {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}

	.source--lg_mod & {
		@include bp($point_2, min) {
			margin-bottom: 2.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.source__title {
	@include h3;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2px;
		}

		@include bp($point_3) {
			margin-bottom: 3px;
		}
	}

	.source--lg_mod & {
		@include h2;

		@include bp($point_2, min) {
			margin-bottom: 0.4rem;
		}

		@include bp($point_3) {
			font-size: 3.6rem;
		}
	}
}

.source__text {
	@include text;

	flex-grow: 1;

	line-height: 1.5;
	color: $black_3;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}

	.source--lg_mod & {
		@include bp($point_2, min) {
			margin-bottom: 4.8rem;
		}
	}
}
