.colored_icon {
	display: block;
	width: 6.4rem;
	height: 6.4rem;
	padding: 1.8rem;

	border-radius: 0.8rem;
	border: 1px solid $gray_7;
	background-color: $white;
	box-shadow: 0 2px 1.8rem 0 rgba(0, 0, 0, 0.05);

	&.colored_icon--sm_mod {
		@include bp($point_2, min) {
			width: 4.8rem;
			height: 4.8rem;
			padding: 1.2rem;
		}

		@include bp($point_3) {
			width: 4rem;
			height: 4rem;
			padding: 1rem;
		}
	}
}
