.form_input {
	&.form_input--full_mod {
		flex-grow: 1;
	}
}

.form_input__label {
	@include form_el_label;

	&.form_input__label--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}
}

.form_input__element {
	@include form_el_default;
}

.form_input__element_v2 {
	@include form_el_v2;
}

.form_input__field {
	position: relative;
	flex-shrink: 0;

	&.form_input__field--icon_mod {
		&::after {
			position: absolute;
			content: '';
			z-index: 10;
			top: 1.2rem;
			left: 1.2rem;
			display: block;
			width: 1.6rem;
			height: 1.6rem;
		}
	}

	&.form_input__field--search_mod {
		@include bp($point_2, min) {
			min-width: 40.4rem;
		}

		&::after {
			background-image: url('data:image/svg+xml;base64,');
		}
	}

	&.form_input__field--mail_mod {
		&::after {
			background-image: url('data:image/svg+xml;base64,');
		}
	}
}
