.team {
}

.team__list {
	@include bp($point_4, min) {
		display: flex;
		flex-wrap: wrap;
		margin-right: -2.4rem;
		margin-bottom: -2.4rem;
	}
}

.team__item {
	@include bp($point_4, min) {
		width: calc(100% / 3);
		padding-right: 2.4rem;
		padding-bottom: 2.4rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			margin-bottom: 2.2rem;
		}
	}
}

.team__member {
	display: flex;
	flex-direction: column;
	align-items: center;

	@include bp($point_2, min) {
		padding: 0 5.2rem;
	}

	@include bp($point_3, $point_4) {
		padding: 0 1rem;
	}
}

.team__member_media {
	max-width: 30rem;
	width: 100%;

	&:not(:last-child) {
		margin-bottom: 2.8rem;
	}
}

.team__member_pic {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	padding-bottom: 100%;

	border-radius: 50%;
	overflow: hidden;
}

.team__member_img {
	@include stretch;
	@include cover_img;
}

.team__member_name {
	@include h3;

	text-align: center;
}

.team__member_position {
	@include text;

	text-align: center;
	color: $black_3;

	&:not(:last-child) {
		margin-bottom: 1.4rem;
	}
}

.team__member_link {
	display: block;
	width: 3.6rem;
	height: 3.6rem;
	padding: 0.6rem;

	color: $blue_2;

	border-radius: 0.8rem;
	background-color: transparent;
	transition: background-color 0.3s ease;

	&:hover,
	&:focus {
		background-color: $black_4;
	}
}
