.brand_assets {
}

.brand_assets__list_w {
	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2rem;
		}
	}
}

.brand_assets__list {
	@include bp($point_4, min) {
		display: flex;
		flex-wrap: wrap;
		margin-right: -2.4rem;
		margin-bottom: -2.4rem;
	}
}

.brand_assets__item {
	@include bp($point_4, min) {
		width: 50%;
		padding-right: 2.4rem;
		padding-bottom: 2.4rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			margin-bottom: 1.6rem;
		}
	}
}

.brand_assets__block {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	height: 100%;
	padding: 3.2rem;

	border-radius: 1.2rem;
	background-color: $black_5;
	overflow: hidden;
}

.brand_assets__block_list {
	flex-grow: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;

	.brand_assets__block--variant_0_mod & {
		gap: 2.4rem;
	}

	.brand_assets__block--variant_1_mod & {
		@include bp($point_4, min) {
			gap: 1.6rem;
		}

		@include bp($point_4 - 1) {
			gap: 0.9rem;
		}
	}

	&:not(:last-child) {
		margin-bottom: 2.6rem;
	}
}

.brand_assets__block_list_item {
	.brand_assets__block--variant_0_mod & {
		width: 6rem;
		height: 6rem;
	}

	.brand_assets__block--variant_1_mod & {
		@include bp($point_2, min) {
			width: 13.8rem;
			height: 6rem;
		}

		@include bp($point_3) {
			width: 8.4rem;
			height: 3.7rem;
		}
	}
}

.brand_assets__block_list_img {
	display: block;
	width: 100%;
	height: 100%;
}

.brand_assets__block_title {
	@include h3;

	text-align: center;
	color: $black_2;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}
}

.brand_assets__block_text {
	@include text;

	max-width: 41.2rem;

	line-height: 1.5;
	text-align: center;
	color: $black_3;
}

.brand_assets__btn {
	display: flex;
	justify-content: center;
}

.brand_assets__btn_icon {
	flex-shrink: 0;
	display: block;
	width: 1.6rem;
	height: 1.6rem;
}
