@mixin form_el_radio {
	position: relative;

	padding-left: 3rem;

	font-size: 1.6rem;
	color: $black;
	cursor: pointer;

	&:before {
		content: '';
		position: absolute;
		top: -1px;
		left: 0;
		width: 2rem;
		height: 2rem;
		border-radius: 100%;
		border: 1px solid $black;
		cursor: pointer;
	}

	&:after {
		@include transition_opacity;

		content: '';
		position: absolute;
		top: 0.4rem;
		left: 0.5rem;
		width: 1rem;
		height: 1rem;
		border-radius: 100%;
		background: $black;
		opacity: 0;

		.form_radio__element:checked + & {
			opacity: 1;
		}
	}
}

@mixin form_el_radio_2 {
	position: relative;
	display: block;
	width: 100%;
	padding: 0.5rem 3.2rem 0.5rem 1.2rem;

	font-size: 1.4rem;
	font-weight: 400;
	color: $black_2;

	border-radius: 4px;
	cursor: pointer;

	.form_radio__element:checked + & {
		font-weight: 600;
		background-color: $color_info_bg;
	}

	&:hover,
	&:focus {
		background-color: $gray_8;
	}

	&:after {
		position: absolute;
		content: '';
		z-index: 1;
		top: 0.4rem;
		right: 1rem;
		display: block;
		width: 2rem;
		height: 2rem;

		background-image: url('data:image/svg+xml;base64,');

		opacity: 0;

		.form_radio__element:checked + & {
			opacity: 1;
		}
	}
}

@mixin form_el_radio_3 {
	position: relative;
	display: block;
	width: 100%;
	padding: 0.5rem 3.2rem 0.5rem 1.2rem;

	font-size: 1.4rem;
	font-weight: 400;
	color: inherit;

	border-radius: 4px;
	cursor: pointer;

	&:hover,
	&:focus {
		background-color: $gray_8;
	}
}
