@mixin st_title {
	font-family: $font_2;
	font-weight: 600;
	color: $black_2;
}

@mixin h1 {
	@include st_title;

	@include bp($point_2, min) {
		font-size: 4.8rem;
		line-height: 1.16;
	}

	@include bp($point_3) {
		font-size: 3.6rem;
		line-height: 1.44;
	}
}

@mixin h2 {
	@include st_title;

	line-height: 1.44;

	@include bp($point_2, min) {
		font-size: 3.6rem;
	}

	@include bp($point_3) {
		font-size: 3.2rem;
	}
}

@mixin h3 {
	@include st_title;

	font-size: 2.4rem;
	line-height: 1.33;
}

@mixin h4 {
	@include st_title;

	font-size: 2rem;
	line-height: 1.4;
}

@mixin h5 {
	@include st_title;

	font-size: 1.6rem;
	line-height: 1.5;
}

@mixin h6 {
	@include st_title;

	font-size: max(1.4rem, 10px);
}

@mixin category_label {
	font-size: 1.4rem;
	font-weight: 500;
	text-transform: uppercase;
}

@mixin text {
	font-size: 1.6rem;
	line-height: 1.4;
}

@mixin text_2 {
	font-size: max(1.4rem, 10px);
	line-height: 1.6;
}

@mixin text_3 {
	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.4;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		line-height: 1.5;
	}
}

// offsets

@mixin title_offset {
	margin-bottom: 2rem;
}