.ideas {
}

.ideas__heading {
	max-width: 104rem;
	margin-inline: auto;

	&:not(:last-child) {
		margin-bottom: 3.2rem;
	}
}

.ideas__list {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(23rem, 1fr));

	@include bp($point_2, min) {
		gap: 2rem;
	}

	@include bp($point_3) {
		gap: 3.2rem;
	}
}

