.testimonials_item {
	height: 100%;
}

.testimonials_item__in {
	width: 100%;
	height: 100%;
	padding: 2.4rem;

	background-color: $black_5;
	border: 1px solid $border_secondary;
	border-radius: 0.8rem;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 2.4rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: 1.6rem;
		}
	}
}

.testimonials_item__head {
	display: flex;
	align-items: center;
	gap: 1.2rem;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.testimonials_item__avatar {
	display: block;
	width: 4rem;
	height: 4rem;

	border-radius: 50%;
	overflow: hidden;
}

.testimonials_item__avatar_img {
	@include cover_img;
}

.testimonials_item__name {
	@include h5;

	color: $black_2;
}

.testimonials_item__text {
	@include text;

	line-height: 1.5;
	color: $black_2;

	&:not(:last-child) {
		margin-bottom: 1.2rem;
	}
}

.testimonials_item__rating {
	display: flex;
	gap: 1.2rem;
}

.testimonials_item__star {
	width: 2.4rem;
	height: 2.4rem;
	color: $primary_hover;

	&.testimonials_item__star--opacity_mod {
		opacity: 0.2;
	}
}

.testimonials_item__screens {
	flex-wrap: wrap;
	display: flex;
	@include bp($point_4, min) {
		margin: 0 -2.4rem -2.4rem 0;
	}
	@include bp($point_4 - 1) {
		margin: 0 -1.6rem -1.6rem 0;
	}
}

@mixin get_col_size($first, $second) {
	&:nth-child(2n + 1) {
		width: $first;
	}
	&:nth-child(2n + 2) {
		width: $second;
	}
}

.testimonials_item__screens_col {
	width: 100%;
	@include bp($point_4, min) {
		padding: 0 2.4rem 2.4rem 0;
	}
	@include bp($point_4 - 1) {
		padding: 0 1.6rem 1.6rem 0;
	}

	.testimonials_item--screens_mod & {
		@include get_col_size(42%, 58%);
	}
	.testimonials_item--screens_2_mod & {
		@include get_col_size(50%, 50%);
	}
	.testimonials_item--screens_3_mod & {
		@include bp($point_4, min) {
			@include get_col_size(43%, 57%);
		}
		@include bp($point_4 - 1) {
			@include get_col_size(50%, 50%);
		}
	}

	&.testimonials_item__screens_col--full_mod {
		min-width: 100%;
	}
}

.testimonials_item__screens_item {
	background-color: #f1f1f1;
	border-radius: 0.8rem;
	border: 2px solid #bae0ff;
	overflow: hidden;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 2.4rem;
		}
		@include bp($point_4 - 1) {
			margin-bottom: 1.6rem;
		}
	}
}

.testimonials_item__screen {
	display: block;
	width: 100%;
	height: 100%;
	// border: 1px solid #dedede;
	// border-radius: 0.8rem;
}

.testimonials_item__screen_img {
	@include contain_img;
}
