$default_indent: 1.2rem;

.fancybox__content img {
	border-radius: 0.8rem;

	@include bp($point_2, min) {
		cursor: zoom-out;
	}
}

.article_content {
	font-size: 1.8rem;
	line-height: 1.56;
	color: $black_2;

	[data-fancybox] {
		@include bp($point_2, min) {
			cursor: zoom-in;
		}
	}

	&:not(:last-child) {
		margin-bottom: 3rem;
	}

	h1 {
		@include h1;

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}

	h2 {
		@include h2;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	h3 {
		@include h3;

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}

	h4 {
		@include h4;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	h5 {
		@include h5;

		&:not(:last-child) {
			margin-bottom: .8rem;
		}
	}

	h6 {
		@include h6;

		&:not(:last-child) {
			margin-bottom: .6rem;
		}
	}

	strong {
		font-weight: 600;
	}

	s {
		text-decoration: line-through;
	}

	u {
		text-decoration: underline;
	}

	em {
		font-style: italic;
	}

	a:not(.key_features a, .article_banner a) {
		color: $primary;
		text-decoration: underline;

		transition: color .3s;

		&:hover,
		&:focus {
			text-decoration: none;
			color: $primary_hover;
		}
	}

	p:not(:last-child) {
		margin-bottom: $default_indent;
	}

	figure {
		display: block;
		width: 100%;
		height: 100%;

		overflow: hidden;
		border-radius: .8rem;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}

		img {
			@include contain_img;

			&:not(:last-child) {
				margin-bottom: .6rem;
			}
		}

		figcaption {
			@include text_2;

			color: $black_3;
		}
	}

	ul:not(.key_features ul, .article_banner ul),
	ol:not(.key_features ol, .article_banner ol) {
		padding-left: 2.7rem;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	ul:not(.key_features ul, .article_banner ul) {
		li {
			list-style-type: disc;
		}
	}

	ol:not(.key_features ol, .article_banner ol) {
		li {
			list-style-type: decimal;
		}
	}

	blockquote {
		padding-left: 2.4rem;
		border-left: 2px solid $primary;

		&:not(:last-child) {
			margin-bottom: $default_indent;
		}
	}

	hr {
		border: none;
		margin: 0;
		border-top: 2px solid $primary;
	}
}