.faq_item {
	width: 100%;

	background-color: rgba($black, 0.02);
	border: 1px solid $border_secondary;
	border-radius: 0.8rem;

	transition: background-color 0.3s ease, border-color 0.3s ease;

	&.faq_item--active_state {
		background-color: $geekblue_2;
		border-color: $geekblue_1;
	}

	@media (any-hover: hover) {
		&:not(.faq_item--active_state):hover {
			background-color: rgba($black, 0.06);
			border-color: $gray_6;
		}
	}
}

.faq_item__head {
	display: flex;
	width: 100%;
	padding: 2rem 2.8rem 1.8rem;

	transition: color 0.3s ease;
	cursor: pointer;

	@include bp($point_2, min) {
		gap: 1.8rem;
	}

	@include bp($point_3) {
		gap: 1.6rem;
	}

	@media (any-hover: hover) {
		&:hover {
			color: $black;
		}
	}
}

.faq_item__title {
	display: block;

	font-family: $font_2;
	font-size: 2rem;
	font-weight: 600;
	line-height: 1.4;
	text-align: start;
	color: $black_2;
}

.faq_item__icon {
	display: block;
	width: 2rem;
	height: 2.2rem;
	padding-top: 2px;

	flex-shrink: 0;

	color: $primary;

	transition: transform 0.3s ease, color 0.3s ease;

	.faq_item--active_state & {
		transform: rotate(90deg);
	}

	.faq_item--active_state & {
		color: $gray_4;
	}

	.faq_item--active_state:hover & {
		color: $primary;
	}
}

.faq_item__body {
	max-height: 0;
	opacity: 0;

	transition: max-height 0.3s ease, opacity 0.3s ease;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.faq_item--active_state & {
		opacity: 1;
		pointer-events: auto;
	}
}

.faq_item__content {
	font-size: 1.6rem;
	line-height: 1.5;
	color: $gray_10;

	@include bp($point_2, min) {
		padding: 0 2rem 2rem 6.6rem;
	}

	@include bp($point_3) {
		padding: 0 2rem 2rem 6.4rem;
	}

	p:not(:last-child) {
		margin-bottom: 1rem;
	}
}