.testimonials_total {
	display: flex;
	align-items: center;

	@include bp($point_4, min) {
		gap: 2rem;
	}

	@include bp($point_4 - 1) {
		flex-direction: column;
		text-align: center;
	}

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 3.2rem;
		}
	}
}

.testimonials_total__column {
	&:last-child {
		@include bp($point_4, min) {
			margin-left: auto;
		}
	}

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			display: none;
			// margin-bottom: 1.6rem;
		}
	}
}

.testimonials_total__rating {
	@include bp($point_4, min) {
		@include h3;
		color: $black_10;
	}

	@include bp($point_4 - 1) {
		font-family: $font_2;
		font-size: 1.6rem;
		line-height: 1.5;
		font-weight: 600;
		color: $black_2;

		text-align: center;
	}

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 0.6rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 1rem;
		}
	}

	strong {
		font-weight: inherit;

		@include bp($point_4, min) {
			margin-left: 0.3rem;
			color: $primary;
		}

		@include bp($point_4 - 1) {
			margin-left: 0.4rem;
		}
	}
}

.testimonials_total__wrap {
	font-weight: 500;
	font-size: 1.6rem;
	line-height: 1.5;

	@include bp($point_4, min) {
		display: flex;
		gap: 0.8rem;
		align-items: center;
	}
}

.testimonials_total__count {
	color: $black_10;

	@include bp($point_4, min) {
		order: 0;
	}
}

.testimonials_total__rating_in {
	display: flex;
	gap: 0.4rem;
	align-items: center;

	@include bp($point_4, min) {
		order: 1;
	}

	@include bp($point_4 - 1) {
		justify-content: center;
	}

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			margin-bottom: 0.4rem;
		}
	}
}

.testimonials_total__rating_text {
	color: $black_8;

	font-size: 1.6rem;
	font-weight: 500;
	line-height: 1;
}

.testimonials__guarantee {
	display: flex;
	gap: 1.2rem;
	align-items: center;
	padding-left: 0.8rem;
}

.testimonials__content {
	.testimonials_total:not(.testimonials_total--layout_mod) & {
		max-width: 24rem;
	}
}

.testimonials__text {
	@include category_label;

	line-height: 1.57;
	color: rgba($black, 0.88);

	.testimonials_total:not(.testimonials_total--layout_mod) & {
		&:not(:last-child) {
			margin-bottom: 2px;
		}
	}

	@include bp($point_4, min) {
		text-align: right;
		order: 0;
	}

	@include bp($point_4 - 1) {
		text-align: left;
		padding-right: 2rem;
	}
}

.testimonials__text_sub {
	font-size: 1.2rem;
	line-height: 1.6rem;
	text-align: left;
	color: rgba($black, 0.45);
}

.testimonials__picture {
	display: block;
	width: 8rem;
	height: 8rem;
	flex-shrink: 0;

	.testimonials_total--layout_mod & {
		@include bp($point_4, min) {
			order: 1;
		}
	}
}

.testimonials__img {
	@include contain_img;
}

.testimonials__list_w {
	position: relative;
	will-change: height;

	overflow: hidden;
	will-change: max-height;
	transition: max-height 0.3s ease-in-out;

	@include bp($point_4, min) {
		max-height: 92.6rem;
	}

	@include bp($point_4 - 1) {
		// max-height: 110rem;
		max-height: unset !important;
	}

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 4.6rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2.4rem;
		}
	}

	&::after {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 32.2rem;
		max-height: 100%;

		background: linear-gradient(180deg, rgba(249, 249, 250, 0) 67.88%, $white 100%);
		transition: opacity 0.3s ease;
		pointer-events: none;

		@include bp($point_4, min) {
			content: '';
		}
	}

	&.testimonials__list_w--open_state::after {
		opacity: 0;
	}
}

.testimonials__list {
	column-gap: 0;

	@include bp($point_2, min) {
		column-count: 3;
	}

	@include bp($point_3, $point_4) {
		column-count: 2;
	}

	@include bp($point_4 - 1) {
		display: flex;
		flex-direction: column;
		gap: 1.6rem;
	}

	@include bp($point_4, min) {
		margin-right: -2.4rem;
		margin-bottom: -2rem;
	}
}

.testimonials__item {
	display: inline-flex;
	width: 100%;

	@include bp($point_4, min) {
		padding-right: 2.4rem;
		padding-bottom: 2rem;
	}

	@include bp($point_4 - 1) {
		order: 1;
	}

	&.testimonials__item--offset_mod {
		@include bp($point_2, min) {
			margin-top: 4rem;
		}
	}

	&.testimonials__item--mobile_order_mod {
		@include bp($point_4 - 1) {
			order: 0;
			display: block;
		}
	}

	.testimonials__list_w:not(.testimonials__list_w--open_state) &:not(.testimonials__item--mobile_order_mod) {
		@include bp($point_4 - 1) {
			display: none;
		}
	}
}

.testimonials__btn_w {
	display: flex;
	justify-content: center;
}
