.form_radio__block {
	display: block;

	color: $black_2;
	border-radius: 4px;

	&.form_radio__block--active_state {
		background-color: $color_info_bg;
		color: $primary;
	}
}

.form_radio__element {
	display: none;
}

.form_radio__label {
	@include form_el_radio;
}

.form_radio__label_2 {
	@include form_el_radio_2;
}

.form_radio__label_3 {
	@include form_el_radio_3;
}
