.banner {}

.banner__in {
	position: relative;

	background-color: $geekblue_2;
	border-radius: $gl_radius;
	border: 1px solid $geekblue_1;
	overflow: hidden;

	@include bp($point_2, min) {
		padding: 3.2rem;
	}

	@include bp($point_3) {
		padding: 2.4rem 2.4rem 0 2.4rem;
	}

	.banner-v2_mod & {
		@include bp($point_3) {
			padding-bottom: 2.4rem;
		}
	}
}

.banner__row {
	@include bp($point_2, min) {
		display: flex;
	}
}

.banner__column {
	&:nth-child(2n+1) {
		@include bp($point_2, min) {
			width: 54%;
		}
	}

	&:nth-child(2n+2) {
		@include bp($point_2, min) {
			width: 46%;
		}
	}

	.banner-v1_mod & {
		&:not(:last-child) {
			@include bp($point_3) {
				margin-bottom: 2rem;
			}
		}
	}

	.banner-v2_mod & {
		&:not(:last-child) {
			@include bp($point_3) {
				margin-bottom: 3.2rem;
			}
		}
	}
}

.banner__decor {
	display: flex;
	align-items: center;
	justify-content: center;

	width: 6.4rem;
	height: 6.4rem;
	border-radius: .8rem;

	background-color: $white;
	border: 1px solid $gray_7;
	box-shadow: 0 2px 1.8rem 0 rgba($black, .05);


	&:not(:last-child) {
		margin-bottom: 2.8rem;
	}
}

.banner__icon {
	display: block;
	width: 2rem;
	height: 2rem;
	color: #FFC53D;
}

.banner__content {
	h2 {
		@include st_title;

		line-height: 1.44;
		font-size: 3.6rem;

		&:not(:last-child) {
			margin-bottom: .8rem;
		}
	}

	p {
		font-size: 2rem;
		line-height: 1.4;
		color: $black_3;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 5.6rem;
			}

			@include bp($point_3) {
				margin-bottom: 2.4rem;
			}
		}
	}

	.banner-v2_mod & {
		@include bp($point_2, min) {
			max-width: 54rem;
		}

		p:not(:last-child) {
			margin-bottom: 2.4rem;
		}

		a {
			@include bp($point_4 - 1) {
				width: 100%;
			}
		}
	}
}

.banner__picture {
	@include bp($point_2, min) {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
	}

	@include bp($point_3) {
		position: relative;
		display: block;
	}

	.banner--v1_mod & {
		@include bp($point_2, min) {
			right: -4.2rem;

			width: 55rem;
			height: 67.8rem;
		}

		@include bp($point_3) {
			margin-left: -2.4rem;

			width: calc(100% + 4.8rem);
			padding-bottom: 94.2%;
		}
	}

	.banner-v2_mod & {
		@include bp($point_2, min) {
			width: 62rem;
			height: 41.6rem;
			right: 0;
		}

		@include bp($point_3) {
			width: calc(100% + 1.6rem);
			margin-left: -.8rem;
			padding-bottom: 64%;
		}
	}
}

.banner__img {
	@include contain_img;

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;

		object-position: center bottom;
	}
}