.testimonials_item_v2 {
	width: 100%;
	height: 100%;
	padding: 2.4rem;

	background-color: $black_5;
	border: 1px solid $border_secondary;
	border-radius: 0.8rem;
}

.testimonials_item_v2__head {
	display: flex;
	align-items: center;
	gap: 1.2rem;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.testimonials_item_v2__avatar {
	display: block;
	width: 4rem;
	height: 4rem;

	border-radius: 50%;
	overflow: hidden;
}

.testimonials_item_v2__avatar_img {
	@include cover_img;
}

.testimonials_item_v2__name {
	@include h5;

	color: $black_2;
}

.testimonials_item_v2__text {
	@include text;

	line-height: 1.5;
	color: $black_2;

	&:not(:last-child) {
		margin-bottom: 1.2rem;
	}
}

.testimonials_item_v2__rating {
	display: flex;
	gap: 1.2rem;
}

.testimonials_item_v2__star {
	width: 2.4rem;
	height: 2.4rem;
	color: $primary_hover;

	&.testimonials_item_v2__star--opacity_mod {
		opacity: 0.2;
	}
}
