.hottest_articles {
	@include bp($point_2, min) {
		padding-top: 6.2rem;
	}

	@include bp($point_3) {
		padding-top: 5rem;
	}

	&:has(+ .cta--subscribe_mod) {
		@include bp($point_2, min) {
			padding-bottom: 1rem;
		}

		& + .cta--subscribe_mod {
			@include bp($point_2, min) {
				padding-top: 1rem;
			}
		}
	}
}

.hottest_articles__title {
	vertical-align: middle;

	@include bp($point_4 - 1) {
		text-align: center;
	}

	&:not(:last-child) {
		margin-bottom: 2.8rem;
	}
}

.hottest_articles__title_text {
	@include h2;

	color: $black;
}

.hottest_articles__title_icon {
	position: relative;
	bottom: -0.6rem;
	width: 3.8rem;
	height: 3.8rem;
	display: none;

	.hottest_articles--icon_start_mod & {
		&.hottest_articles__title_icon--start_mod {
			display: inline-flex;
		}
	}

	.hottest_articles--icon_end_mod & {
		&.hottest_articles__title_icon--end_mod {
			display: inline-flex;
		}
	}
}
