.supporting__body {
	position: relative;

	background: rgba($black, 0.02);

	overflow: hidden;

	@include bp($point_2, min) {
		border: 1px solid $border_secondary;
		border-radius: $gl_radius_2;
	}

	@include bp($point_3) {
		margin: 0 calc(var(--gl-hr-indent) * -1);
	}
}

.supporting__decor_bg_w {
	position: absolute;
	bottom: 0;

	@include bp($point_2, min) {
		top: -6rem;
		right: -2px;

		width: 46rem;
		height: 46.6rem;
	}

	@include bp($point_3) {
		right: 0;

		width: 37.5rem;
		height: 38rem;
	}
}

.supporting__decor_bg_in {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

.supporting__row {
	position: relative;
	z-index: 1;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);

		padding: 4rem 12rem 0 5.6rem;
	}

	@include bp($point_3) {
		padding: 4.8rem 2rem 4rem;
	}
}

.supporting__col {
	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 4.4rem;
		}
	}

	&.supporting__col--content_mod {
		@include bp($point_2, min) {
			padding: 0 4rem 3.4rem 0;
		}
	}

	&.supporting__col--image_mod {
		@include bp($point_2, min) {
			align-self: flex-end;
			flex-shrink: 0;
			margin-left: auto;
		}

		@include bp($point_3) {
			margin-bottom: -4rem;
		}
	}
}

.supporting__heading {
	max-width: 57.6rem;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.6rem;
		}
	}
}

.supporting__title {
	@include h2;

	line-height: 1.44;
	color: rgba($black, 0.88);

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.supporting__text {
	max-width: 54rem;

	font-size: 1.6rem;
	line-height: 1.5;
	color: rgba($black, 0.65);
}

.supporting__list {
	@include flex_block(row, wrap, flex-start);

	gap: 1.6rem 3.2rem;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.2rem;
		}
	}
}

.supporting__item {
	display: flex;
	align-items: center;
}

.supporting__item_text {
	@include h5;

	line-height: 1.5;
}

.supporting__icon_w {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-shrink: 0;
	width: 4rem;
	height: 4rem;
	padding: 1rem;

	box-shadow: 0 2px 1.8rem 0 rgba($black, 0.05);
	border: 1px solid $gray_7;
	border-radius: $gl_radius_2;

	&:not(:last-child) {
		margin-right: 1.2rem;
	}
}

.supporting__icon_in {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

.supporting__bottom {
	@include flex_block(row, wrap, flex-start, center, center);

	gap: 1.2rem 2.2rem;

	@include bp($point_5 - 1) {
		flex-direction: column;
	}
}

.supporting__btn_block {
	@include bp($point_6 - 1) {
		width: 100%;
	}

	.btn_base {
		@include bp($point_6 - 1) {
			width: 100%;
		}
	}
}

.supporting__label_list {
	display: flex;
	align-items: center;

	font-size: 1.6rem;
	line-height: 1.5;
	color: rgba($black, 0.65);
}

.supporting__label_item {
	&:not(:last-child) {
		&::after {
			margin: 0 0.8rem;

			content: '|';
		}
	}
}

.supporting__image_w {
	@include bp($point_2, min) {
		width: 28.9rem;
		height: 36.6rem;
	}

	@include bp($point_3) {
		max-width: max(23.8rem, 40vw);
		margin: 0 auto;
	}
}

.supporting__image_in {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
	object-position: bottom;

	border-radius: $gl_radius $gl_radius 0 0;
	box-shadow: 0 2px 2.2rem 0 rgba(0, 0, 0, 0.05);
}

.supporting__image_in img {
	width: 100%;
	height: auto;
}
