.shopify_banner {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;

	@include bp($point_2, min) {
		padding: 0.7rem var(--gl-hr-indent);
	}

	@include bp($point_3) {
		padding: 1.4rem var(--gl-hr-indent) 2rem;
	}
}

.shopify_banner__row {
	position: relative;
	z-index: 1;

	@include bp($point_2, min) {
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 2.6rem;
	}
}

.shopify_banner__bg {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}

.shopify_banner__bg_img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.shopify_banner__content {
	display: flex;
	align-items: center;

	@include bp($point_3) {
		margin-bottom: 1.1rem;
	}
}

.shopify_banner__picture {
	width: 3.2rem;
	height: 3.2rem;

	display: flex;
	align-items: center;
	justify-content: center;

	margin-right: 1.4rem;

	background-color: $white;
	border: 1px solid $gray_7;
	border-radius: 4px;
	box-shadow: 0 1px 0.9rem rgba(22, 119, 255, 5%);
}

.shopify_banner__title {
	color: $white;
	font-family: $font_2;
	font-size: 1.6rem;
}

.shopify_banner__button {
	height: 3.2rem;

	font-weight: 500;

	@include bp($point_3) {
		width: 100%;
	}
}

.shopify_banner__button_icon {
	width: 2rem;
	height: 2rem;
}

.shopify_banner__close {
	position: absolute;
	z-index: 2;

	width: 1.4rem;
	height: 1.4rem;

	cursor: pointer;

	@include bp($point_2, min) {
		right: 1.6rem;
		top: 1.6rem;
	}

	@include bp($point_3) {
		top: 2.2rem;
		right: var(--gl-hr-indent);
	}
}
