.help_sources {
}

.help_sources__heading {
	h2 {
		@include bp($point_2, min) {
			max-width: 50.4rem;
			margin: 0 auto;
		}
	}
}

.help_sources__list {
	@include bp($point_4, min) {
		display: flex;
		flex-wrap: wrap;
		margin-right: -2.4rem;
		margin-bottom: -2.4rem;
	}
}

.help_sources__item {
	@include bp($point_4, min) {
		width: 50%;
		padding-right: 2.4rem;
		padding-bottom: 2.4rem;
	}

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			margin-bottom: 1.6rem;
		}
	}

	&.help_sources__item--lg_mod {
		width: 100%;
	}
}
