//------------------------------------------------------------layout
.header {
	position: fixed;
	top: 0;
	left: 0;

	height: var(--height-header);
	width: 100%;
	padding: 1.6rem var(--gl-hr-indent);

	font-size: 1rem;

	will-change: transform, box-shadow;
	transition: box-shadow 0.3s;

	@include z-index(header);

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: var(--height-header);

		background-color: $white;

		@include bp($point_3) {
			z-index: 5;
		}
	}

	.body--open_menu_state & {
		height: 100%;
	}

	&.header--scroll_state {
		&:not(.body--open_menu_state &) {
			box-shadow: 0 2px 0.4rem 0 rgba($black, 0.02), 0 1px 0.6rem -1px rgba($black, 0.02),
				0 1px 2px 0 rgba($black, 0.03);
		}
	}

	@include bp($point_2, min) {
		.body--shopify_banner_mod & {
			top: 4.6rem;
		}
	}

	@include bp($point_3) {
		.body--shopify_banner_mod & {
			top: 10.9rem;
		}
	}
}

.header__in {
	max-width: $page_width;
	width: 100%;
	margin: 0 auto;
}

//------------------------------------------------------------layout###

$gap: 1.6rem;

.header__row {
	display: flex;
	align-items: center;
	gap: $gap;
}

.header__logo {
	position: relative;
	z-index: 5;
	flex: 1 1 auto;
}

.header__logo_link {
	display: block;
	height: 2rem;

	@include bp($point_2, min) {
		width: 12.8rem;
	}

	@include bp($point_3) {
		width: 12.4rem;
	}
}

.header__img {
	@include contain_img;
}

.header__content {
	display: flex;
	gap: $gap;
}

.header__body {
	@include bp($point_3) {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 4;

		display: flex;
		flex-direction: column;

		width: 100%;
		height: 100%;
		min-height: var(--full-screen);
		padding: calc(var(--height-header) - 4px) var(--gl-hr-indent) var(--gl-hr-indent);

		overflow-x: hidden;
		overflow-y: auto;
		background-color: $white;

		opacity: 0;
		pointer-events: none;
		transform: translateY(-20%);

		transition: transform 0.3s ease, opacity 0.3s ease;
	}

	.body--open_menu_state & {
		@include bp($point_3) {
			opacity: 1;
			pointer-events: initial;
			transform: translateY(0);
		}
	}
}

.header_list {
	@include bp($point_2, min) {
		display: flex;
		flex-wrap: wrap;
		gap: $gap;
	}

	@include bp($point_3) {
		flex: 1 1 auto;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 4rem;
		}
	}
}

.header__item {
	position: relative;

	&.header__item--submenu_mod {
		&::before {
			@include bp($point_2, min) {
				content: '';
				position: absolute;
				top: 100%;
				left: 0;
				z-index: 2;

				width: 100%;
				height: 1.2rem;

				pointer-events: none;
			}
		}

		&:hover,
		&:focus {
			&::before {
				@include bp($point_2, min) {
					pointer-events: initial;
				}
			}
		}
	}

	&.header__item--current_state {
		pointer-events: none;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			border-bottom: 1px solid $black_4;
		}
	}
}

.header__link {
	display: flex;
	align-items: center;
	gap: 1rem;

	font-family: $font_1;
	font-size: 1.6rem;
	font-weight: 500;
	line-height: 1.4;
	color: $black_2;
	cursor: pointer;

	transition: color 0.3s, background-color 0.3s;

	@include bp($point_2, min) {
		padding: 0.8rem 1.5rem;
		border-radius: 0.8rem;
		overflow: hidden;
	}

	@include bp($point_3) {
		line-height: 1.5;
		width: 100%;
		padding: 2.4rem 0;
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@media (any-hover: hover) {
		.header__item:hover & {
			@include bp($point_2, min) {
				background-color: $black_4;
			}
		}
	}

	.header__item--current_state & {
		color: $primary;
	}
}

.header__icon {
	display: block;
	flex-shrink: 0;
	width: 1.2rem;
	height: 1.2rem;

	transition: transform 0.3s;

	@include bp($point_3) {
		order: 2;
		margin-left: auto;
		margin-right: 2px;
	}

	.header__item--active_state & {
		@include bp($point_3) {
			transform: rotate(-180deg);
		}
	}

	@media (any-hover: hover) {
		.header__item:hover & {
			@include bp($point_2, min) {
				transform: rotate(-180deg);
			}
		}
	}
}

.header__submenu {
	@include bp($point_2, min) {
		position: absolute;
		top: calc(100% + 1.2rem);

		max-height: unset !important;
		min-width: 58.8rem;

		pointer-events: none;
		opacity: 0;

		transition: opacity 0.3s ease;
	}

	@include bp($point_3) {
		max-height: 0;
		opacity: 0;
		overflow: hidden;
		pointer-events: none;

		transition: max-height 0.3s, opacity 0.3s;
		will-change: max-height;

		.header__item--active_state & {
			opacity: 1;
			pointer-events: initial;
		}
	}

	.header__item:nth-child(5n + 1) & {
		@include bp($point_2, min) {
			left: -4rem;
		}
	}

	.header__item:nth-child(5n + 2) & {
		@include bp($point_2, min) {
			left: -5.5rem;
		}
	}

	.header__item:nth-child(5n + 3) & {
		@include bp($point_2, min) {
			left: -10rem;
		}
	}

	.header__item:nth-child(5n + 4) & {
		@include bp($point_2, min) {
			left: -15rem;
		}
	}

	.header__item:nth-child(5n + 5) & {
		@include bp($point_2, min) {
			left: -20rem;
		}
	}

	.header__item:hover & {
		@include bp($point_2, min) {
			opacity: 1;
			pointer-events: initial;
		}
	}
}

.header__buttons {
	position: relative;
	z-index: 5;

	display: flex;
	gap: $gap;
}

.header__menu_trigger {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		position: relative;
		z-index: 5;

		width: 4rem;
		height: 4rem;

		border-radius: 0.8rem;
		border: 1px solid $gray_6;
		cursor: pointer;

		&::before,
		&::after,
		span {
			position: absolute;
			left: 50%;
			top: 50%;

			width: 1.4rem;
			height: 1px;
			border-radius: 2px;
			overflow: hidden;

			margin-left: -0.7rem;

			transition: transform 0.3s ease;
			will-change: transform;

			background-color: $black_2;
		}

		&::before {
			content: '';
			transform: translateY(-5px);
		}

		&::after {
			content: '';
			transform: translateY(5px);
		}

		.body--open_menu_state & {
			span {
				transform: scale(0);
			}

			&::before {
				transform: rotate(-45deg);
			}

			&::after {
				transform: rotate(45deg);
			}
		}
	}
}
