.sec_calculator__body {
	background: rgba($black, 0.02);
	border: 1px solid $border_secondary;
	border-radius: $gl_radius;
	overflow: hidden;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}
}

.sec_calculator__row {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);

		margin: 0 -6rem -2.4rem 0;
		padding-left: 4.8rem;
	}

	// @include bp($point_3) {
	// 	flex-direction: column-reverse;
	// 	align-items: stretch;
	// }
}

.sec_calculator__column {
	@include bp($point_2, min) {
		padding: 0 6rem 2.4rem 0;
	}

	&.sec_calculator__column--content_mod {
		@include bp($point_2, min) {
			order: 1;
			flex-grow: 1;
		}

		@include bp($point_3) {
			padding: 2.8rem 2.4rem 2.4rem;
		}
	}

	&.sec_calculator__column--calculator_mod {
		@include bp($point_2, min) {
			order: 2;
			flex-shrink: 0;
			width: 56rem;
		}
	}

	.heading {
		@include bp($point_2, min) {
			margin-left: 0;

			text-align: left;
		}

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}
}

.sec_calculator__btn_block {
	@include bp($point_3) {
		text-align: center;
	}

	a {
		@include bp($point_6) {
			width: 100%;
		}
	}
}

.sec_calculator__calc_body {
	position: relative;

	background-color: $geekblue_2;

	overflow: hidden;

	@include bp($point_2, min) {
		padding: 1.6rem 6.4rem;
	}

	@include bp($point_3) {
		padding: 1rem 4.2rem;
	}
}

.sec_calculator__decor {
	@include stretch;
	@include cover_img;
}

.sec_calculator__calc {
	position: relative;
	z-index: 1;
}
