:root {
  --full-screen: calc(var(--vh, 1vh) * 100);
  --height-header: 7.2rem;
  --height-footer: 10rem;
  --gl-hr-indent: 2rem;
}
@media screen and (min-width: 1024px) {
  :root {
    --gl-vr-indent: 4.4rem;
  }
}
@media screen and (max-width: 1023.5px) {
  :root {
    --gl-vr-indent: 3.2rem;
  }
}

h1 a,
h2 a,
h3 a {
  white-space: break-spaces;
  color: inherit;
  display: inline;
}
h1 a:hover, h1 a:focus,
h2 a:hover,
h2 a:focus,
h3 a:hover,
h3 a:focus {
  text-decoration: none;
}
h1 strong,
h2 strong,
h3 strong {
  display: inline;
}

html {
  --height-timer-banner: 6.4rem;
  height: 100%;
  font-weight: 400;
  line-height: 1.4;
  font-family: "Inter", sans-serif;
  font-size: 10px;
}
@media (max-width: 1440px) and (min-width: 1024px) {
  html {
    font-size: 0.6944444444vw;
  }
}
@media screen and (max-width: 374px) {
  html {
    font-size: 2.6666666667vw;
  }
}

body {
  position: relative;
  display: flex;
  height: 100%;
  font-size: 1.6rem;
  color: #000;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
}
@supports (display: grid) {
  body {
    display: block;
  }
}
body.body--open_menu_state {
  overflow: hidden;
}
@media screen and (min-width: 1024px) {
  body.body--shopify_banner_mod {
    padding-top: 4.6rem;
  }
}
@media screen and (max-width: 1023.5px) {
  body.body--shopify_banner_mod {
    padding-top: 10.9rem;
  }
}

.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  height: auto !important;
  overflow-x: hidden;
}

.base {
  width: 100%;
  position: relative;
  padding: var(--height-header) 0 var(--gl-vr-indent);
  flex: 1 0 auto;
}
.body--error_page_mod .base {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.body--timer_banner_mod .base {
  padding: calc(var(--height-header) + var(--height-timer-banner)) 0 var(--gl-vr-indent);
}

a {
  text-decoration: none;
  color: #4aa3df;
}
a:hover, a:focus {
  text-decoration: underline;
}

.editor-styles-wrapper .wp-block {
  margin-bottom: 5rem;
}

html :where(.wp-block) {
  max-width: 1200px;
}

.editor-styles-wrapper {
  overflow-x: hidden;
}

.back_button {
  font-size: 1.6rem;
  line-height: 1.4;
  display: inline-flex;
  font-family: "Poppins", sans-serif;
  color: #000;
  transition: color 0.3s;
}
.back_button:not(:last-child) {
  margin-bottom: 1rem;
}
.back_button:hover, .back_button:focus {
  text-decoration: none;
  color: #1677ff;
}

.fancybox__backdrop {
  --fancybox-bg: #fff;
}

.fancybox__caption {
  --fancybox-color: #000;
}

.fancybox_link {
  padding: 0 1rem;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  height: var(--height-header);
  width: 100%;
  padding: 1.6rem var(--gl-hr-indent);
  font-size: 1rem;
  will-change: transform, box-shadow;
  transition: box-shadow 0.3s;
  z-index: 100;
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--height-header);
  background-color: #fff;
}
@media screen and (max-width: 1023.5px) {
  .header::before {
    z-index: 5;
  }
}
.body--open_menu_state .header {
  height: 100%;
}
.header.header--scroll_state:not(.body--open_menu_state .header.header--scroll_state) {
  box-shadow: 0 2px 0.4rem 0 rgba(0, 0, 0, 0.02), 0 1px 0.6rem -1px rgba(0, 0, 0, 0.02), 0 1px 2px 0 rgba(0, 0, 0, 0.03);
}
@media screen and (min-width: 1024px) {
  .body--shopify_banner_mod .header {
    top: 4.6rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .body--shopify_banner_mod .header {
    top: 10.9rem;
  }
}

.header__in {
  max-width: 126.4rem;
  width: 100%;
  margin: 0 auto;
}

.header__row {
  display: flex;
  align-items: center;
  gap: 1.6rem;
}

.header__logo {
  position: relative;
  z-index: 5;
  flex: 1 1 auto;
}

.header__logo_link {
  display: block;
  height: 2rem;
}
@media screen and (min-width: 1024px) {
  .header__logo_link {
    width: 12.8rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__logo_link {
    width: 12.4rem;
  }
}

.header__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__content {
  display: flex;
  gap: 1.6rem;
}

@media screen and (max-width: 1023.5px) {
  .header__body {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 4;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    min-height: var(--full-screen);
    padding: calc(var(--height-header) - 4px) var(--gl-hr-indent) var(--gl-hr-indent);
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #fff;
    opacity: 0;
    pointer-events: none;
    transform: translateY(-20%);
    transition: transform 0.3s ease, opacity 0.3s ease;
  }
}
@media screen and (max-width: 1023.5px) {
  .body--open_menu_state .header__body {
    opacity: 1;
    pointer-events: initial;
    transform: translateY(0);
  }
}

@media screen and (min-width: 1024px) {
  .header_list {
    display: flex;
    flex-wrap: wrap;
    gap: 1.6rem;
  }
}
@media screen and (max-width: 1023.5px) {
  .header_list {
    flex: 1 1 auto;
  }
}
@media screen and (max-width: 1023.5px) {
  .header_list:not(:last-child) {
    margin-bottom: 4rem;
  }
}

.header__item {
  position: relative;
}
@media screen and (min-width: 1024px) {
  .header__item.header__item--submenu_mod::before {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 1.2rem;
    pointer-events: none;
  }
}
@media screen and (min-width: 1024px) {
  .header__item.header__item--submenu_mod:hover::before, .header__item.header__item--submenu_mod:focus::before {
    pointer-events: initial;
  }
}
.header__item.header__item--current_state {
  pointer-events: none;
}
@media screen and (max-width: 1023.5px) {
  .header__item:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.06);
  }
}

.header__link {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 1.4;
  color: rgba(0, 0, 0, 0.88);
  cursor: pointer;
  transition: color 0.3s, background-color 0.3s;
}
@media screen and (min-width: 1024px) {
  .header__link {
    padding: 0.8rem 1.5rem;
    border-radius: 0.8rem;
    overflow: hidden;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__link {
    line-height: 1.5;
    width: 100%;
    padding: 2.4rem 0;
  }
}
.header__link:hover, .header__link:focus {
  text-decoration: none;
}
@media screen and (any-hover: hover) and (min-width: 1024px) {
  .header__item:hover .header__link {
    background-color: rgba(0, 0, 0, 0.06);
  }
}
.header__item--current_state .header__link {
  color: #1677ff;
}

.header__icon {
  display: block;
  flex-shrink: 0;
  width: 1.2rem;
  height: 1.2rem;
  transition: transform 0.3s;
}
@media screen and (max-width: 1023.5px) {
  .header__icon {
    order: 2;
    margin-left: auto;
    margin-right: 2px;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__item--active_state .header__icon {
    transform: rotate(-180deg);
  }
}
@media screen and (any-hover: hover) and (min-width: 1024px) {
  .header__item:hover .header__icon {
    transform: rotate(-180deg);
  }
}

@media screen and (min-width: 1024px) {
  .header__submenu {
    position: absolute;
    top: calc(100% + 1.2rem);
    max-height: unset !important;
    min-width: 58.8rem;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__submenu {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    pointer-events: none;
    transition: max-height 0.3s, opacity 0.3s;
    will-change: max-height;
  }
  .header__item--active_state .header__submenu {
    opacity: 1;
    pointer-events: initial;
  }
}
@media screen and (min-width: 1024px) {
  .header__item:nth-child(5n+1) .header__submenu {
    left: -4rem;
  }
}
@media screen and (min-width: 1024px) {
  .header__item:nth-child(5n+2) .header__submenu {
    left: -5.5rem;
  }
}
@media screen and (min-width: 1024px) {
  .header__item:nth-child(5n+3) .header__submenu {
    left: -10rem;
  }
}
@media screen and (min-width: 1024px) {
  .header__item:nth-child(5n+4) .header__submenu {
    left: -15rem;
  }
}
@media screen and (min-width: 1024px) {
  .header__item:nth-child(5n+5) .header__submenu {
    left: -20rem;
  }
}
@media screen and (min-width: 1024px) {
  .header__item:hover .header__submenu {
    opacity: 1;
    pointer-events: initial;
  }
}

.header__buttons {
  position: relative;
  z-index: 5;
  display: flex;
  gap: 1.6rem;
}

@media screen and (min-width: 1024px) {
  .header__menu_trigger {
    display: none;
  }
}
@media screen and (max-width: 1023.5px) {
  .header__menu_trigger {
    position: relative;
    z-index: 5;
    width: 4rem;
    height: 4rem;
    border-radius: 0.8rem;
    border: 1px solid #d9d9d9;
    cursor: pointer;
  }
  .header__menu_trigger::before, .header__menu_trigger::after,
  .header__menu_trigger span {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 1.4rem;
    height: 1px;
    border-radius: 2px;
    overflow: hidden;
    margin-left: -0.7rem;
    transition: transform 0.3s ease;
    will-change: transform;
    background-color: rgba(0, 0, 0, 0.88);
  }
  .header__menu_trigger::before {
    content: "";
    transform: translateY(-5px);
  }
  .header__menu_trigger::after {
    content: "";
    transform: translateY(5px);
  }
  .body--open_menu_state .header__menu_trigger span {
    transform: scale(0);
  }
  .body--open_menu_state .header__menu_trigger::before {
    transform: rotate(-45deg);
  }
  .body--open_menu_state .header__menu_trigger::after {
    transform: rotate(45deg);
  }
}

.header_sublist {
  position: relative;
}
@media screen and (min-width: 1024px) {
  .header_sublist {
    display: flex;
    flex-wrap: wrap;
    padding: 0.8rem;
    background-color: #fff;
    border-radius: 0.8rem;
    box-shadow: 0 0.9rem 2.8rem 0.8rem rgba(0, 0, 0, 0.05), 0 3px 0.6rem -0.4rem rgba(0, 0, 0, 0.12), 0 0.6rem 1.6rem 0 rgba(0, 0, 0, 0.08);
  }
}
@media screen and (max-width: 1023.5px) {
  .header_sublist {
    padding-top: 2px;
    padding-bottom: 3.4rem;
  }
}
@media screen and (min-width: 1024px) {
  .header_sublist::before {
    content: "";
    position: absolute;
    z-index: 2;
    top: 0;
    left: 21%;
    transform: translate(-50%, -100%);
    border-width: 0 0.8rem 0.8rem 0.8rem;
    border-style: solid;
    border-color: transparent transparent #fff transparent;
  }
}

@media screen and (min-width: 1024px) {
  .header_sublist__item {
    width: 50%;
  }
}
@media screen and (max-width: 1023.5px) {
  .header_sublist__item:not(:last-child) {
    margin-bottom: 2rem;
  }
}

.header_sublist__link {
  display: flex;
  gap: 1.2rem;
}
@media screen and (min-width: 1024px) {
  .header_sublist__link {
    padding: 1.2rem;
    border-radius: 0.8rem;
    transition: background 0.3s;
  }
}
@media screen and (max-width: 1023.5px) {
  .header_sublist__link {
    transition: opacity 0.3s;
  }
}
.header_sublist__link:hover, .header_sublist__link:focus {
  text-decoration: none;
}
@media screen and (max-width: 1023.5px) {
  .header_sublist__link:hover, .header_sublist__link:focus {
    opacity: 0.8;
  }
}
@media screen and (any-hover: hover) and (min-width: 1024px) {
  .header_sublist__link:hover {
    background-color: rgba(0, 0, 0, 0.06);
  }
}

.header_sublist__icon {
  flex-shrink: 0;
  display: block;
  width: 2.4rem;
  height: 2.4rem;
  color: #1677ff;
}

.header_sublist__wrap {
  display: block;
}

.header_sublist__title {
  display: block;
  font-size: 1.6rem;
  line-height: 1.5;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.88);
}
.header_sublist__title:not(:last-child) {
  margin-bottom: 2px;
}

.header_sublist__text {
  display: block;
  font-size: 1.4rem;
  line-height: 1.57;
