.anchors {
	@include bp($point_2, min) {
		padding-bottom: 0;
	}
}

.anchors__body {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 2rem;
}

.anchors__label {
	display: flex;
	align-items: center;
	gap: 0.8rem;
	padding: 0.7rem 1.2rem;

	font-size: 1.6rem;
	line-height: 1.5;
	font-weight: 600;
	color: $black_2;

	background-color: $color_info_bg;
	border: 1px solid $color_info_border;
	border-radius: 0.8rem;
}

.anchors__icon {
	flex-shrink: 0;
	width: 1.6rem;
	height: 1.6rem;
}

.anchors__heading {
	&:not(:last-child) {
		margin-bottom: -4px;
	}
}

.anchors__list {
	@include flex_block(row, wrap, center, center, center);

	@include bp($point_2, min) {
		gap: 1.5rem;
	}

	@include bp($point_3) {
		gap: 1.2rem;
	}
}

.anchors__item {
}
