.social_media {
	.section_in {
		max-width: $page_width_2;
	}
}

.social_media__card {
	position: relative;
	padding: 3.2rem 3.2rem 2.8rem;

	background-color: $geekblue_2;
	border-radius: 1.2rem;
	border: 1px solid $geekblue_1;
	overflow: hidden;

	@include bp($point_3) {
		min-height: 49rem;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.4rem;
		}
	}
}

.social_media__card_pic {
	@include stretch;
}

.social_media__card_img {
	@include stretch;
	@include cover_img;
}

.social_media__card_content {
	position: relative;
	z-index: 1;

	flex-shrink: 0;
}

.social_media__followers {
	display: flex;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 8rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.4rem;
		}
	}
}

.social_media__follower {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 5.6rem;
	height: 5.6rem;

	font-size: 1.6rem;

	border-radius: 50%;
	overflow: hidden;
	background-color: $white;

	&:not(:first-child) {
		margin-left: -2rem;
	}

	&.social_media__follower--info_mod {
		color: $black_2;

		border: 1px solid $gray_7;
		box-shadow: 0 2px 1.8rem 0 rgba(0, 0, 0, 0.05);
	}
}

.social_media__follower_pic {
	display: block;
	width: 100%;
	height: 100%;
}

.social_media__follower_img {
	@include cover_img;
}

.social_media__statistics_item {
	font-family: $font_2;
	font-size: 2rem;
	font-weight: 600;
	color: $black_3;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	strong {
		margin-right: 0.4rem;

		font-size: 2.4rem;
		font-weight: inherit;
		color: $black_2;
	}
}

.social_media__links {
	display: flex;
	flex-wrap: wrap;
	gap: 1.6rem;
	justify-content: center;
	align-items: center;

	@include bp($point_4 - 1) {
		max-width: 21rem;
		margin: 0 auto;
	}
}

.social_media__link {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 4rem;
	height: 4rem;
	padding: 1.1rem;

	border-radius: 0.8rem;
	border: 1px solid $gray_6;
	background-color: $white;
	box-shadow: 0 2px 0 0 rgba(0, 0, 0, 0.02);
	transition: background-color 0.3s ease;

	&:hover,
	&:focus {
		background-color: $gray_7;
	}
}

.social_media__link_img {
	@include contain_img;
}
