.progress_bar {
	position: fixed;
	top: var(--height-header);
	left: 0;

	width: 100%;
	height: .5rem;

	pointer-events: none;
	overflow: hidden;

	@include z-index(progress-bar);
}

.progress_bar__fill {
	display: block;
	width: 100%;
	height: 100%;

	background-color: $primary;

	transform: translateX(100%);
	will-change: transform;
}