.automate_store__body {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.automate_store__row {
	@include bp($point_5, min) {
		@include flex_block(row, wrap, flex-start);

		margin: 0 -4.8rem -4.8rem 0;
	}
}

.automate_store__col {
	position: relative;

	@include bp($point_5, min) {
		flex-shrink: 0;
		padding: 0 4.8rem 4.8rem 0;
	}

	@include bp($point_2, min) {
		width: 25%;
	}

	@include bp($point_3, $point_5) {
		width: 50%;
	}

	&:not(:last-child) {
		@include bp($point_5 - 1) {
			padding-bottom: 6rem;
		}
	}
}

.automate_store__icon-w {
	position: absolute;

	flex-shrink: 0;
	width: 3.2rem;
	height: 3.2rem;
	padding: 0.9rem 0.9rem 0.9rem 1rem;

	color: $gray_9;

	border-radius: 50%;
	background-color: $gray_8;

	@include bp($point_5, min) {
		right: 0.8rem;
		top: 8.8rem;

		margin-top: -1.6rem;
	}

	@include bp($point_5 - 1) {
		bottom: 1.2rem;
		left: 50%;

		margin-left: -1.6rem;

		transform: rotate(90deg);
	}

	.automate_store__col:nth-child(4n + 4) & {
		@include bp($point_2, min) {
			display: none;
		}
	}

	.automate_store__col:nth-child(2n + 2) & {
		@include bp($point_3, $point_5) {
			display: none;
		}
	}

	.automate_store__col:last-child & {
		@include bp($point_5 - 1) {
			display: none;
		}
	}
}

.automate_store__body_content {
	@include bp($point_3) {
		max-width: max(28rem, 40vw);
		margin: 0 auto;
	}
}

.automate_store__img_w {
	display: block;
	aspect-ratio: 280/142;

	&:not(:last-child) {
		margin-bottom: 1.4rem;
	}
}

.automate_store__img_in {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

.automate_store__content {
	text-align: center;
}

.automate_store__title {
	@include h4;

	&:not(:last-child) {
		margin-bottom: 0.6rem;
	}
}

.automate_store__text {
	font-size: 1.6rem;
	color: rgba($black, 0.65);
	line-height: 1.5;
}

.automate_store__btn_block {
	position: relative;
	z-index: 1;

	text-align: center;
}