.hero {
	@include bp($point_2, min) {
		display: flex;
		flex-direction: column;

		padding-top: 0;
	}

	@include bp($point_3) {
		padding-top: 5.2rem;

		background-color: rgba($black, 0.02);
		padding-bottom: 0;
		margin-bottom: var(--gl-vr-indent);
	}
}

.hero__in {
	position: relative;

	@include bp($point_2, min) {
		display: flex;
		align-items: flex-end;
		flex: 1 1 auto;

		min-height: 51.8rem;
		padding: var(--gl-vr-indent) 4rem 5.4rem;

		border-radius: 0.8rem;
		border: 1px solid $border_secondary;
		background-color: rgba($black, 0.02);
		overflow: hidden;
	}
}

.hero__row {
	@include bp($point_2, min) {
		display: flex;
		margin: 0 -2rem -2rem 0;
	}
}

.hero__column {
	@include bp($point_2, min) {
		padding: 0 2rem 2rem 0;
	}

	&:nth-child(2n + 1) {
		@include bp($point_2, min) {
			width: 58%;
		}
	}

	&:nth-child(2n + 2) {
		@include bp($point_2, min) {
			width: 42%;
		}
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 2.8rem;
		}
	}
}

.hero__content {
	@include bp($point_3) {
		text-align: center;
	}

	h1 {
		@include h1;

		&:not(:last-child) {
			@include bp($point_2, min) {
				font-size: 4.3rem;
				margin-bottom: 1.8rem;
			}

			@include bp($point_3) {
				margin-bottom: 1rem;
			}
		}
	}

	strong {
		@include word_decor;

		display: inline-block;
		font-weight: inherit;
	}

	p,
	h2 {
		color: $black_3;
		font-weight: 400;

		@include bp($point_2, min) {
			font-size: 2rem;
			line-height: 1.4;
		}

		@include bp($point_3) {
			font-size: 1.6rem;
			line-height: 1.5;
		}
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.8rem;
		}

		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}
}

.hero__buttons {
	display: flex;
	flex-wrap: wrap;
	gap: 1.2rem;

	@include bp($point_3) {
		justify-content: center;
	}

	&:not(:last-child) {
		margin-bottom: 2.8rem;
	}
}

.hero__button,
.hero__button a {
	@include bp($point_6 - 1) {
		width: 100%;
	}
}

.hero__container {
	overflow: hidden;

	@include bp($point_3) {
		height: 71.46vw;
		margin: 0 calc(var(--gl-hr-indent) * -1);
	}
}

.hero__circle {
	pointer-events: none;

	@include bp($point_2, min) {
		position: absolute;
		top: 0;
		right: -17rem;
		z-index: 2;

		width: 72.6rem;
		height: 72.6rem;
	}

	@include bp($point_3) {
		position: relative;

		width: 100vw;
		height: 100vw;
	}
}

.hero__circle_in {
	position: relative;

	display: flex;
	align-items: center;
	justify-content: center;

	height: 100%;
	width: 100%;
}

.hero__decor {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 1;

	background-color: rgba($blue, 0.04);
	aspect-ratio: 1 / 1;
	border-radius: 50%;

	&.hero__decor--v0_mod {
		width: 100%;

		background-color: rgba($blue, 0.02);
	}

	&.hero__decor--v1_mod {
		width: 69.54%;
	}

	&.hero__decor--v2_mod {
		width: 44.22%;
	}

	&.hero__decor--v3_mod {
		width: 25.08%;
	}
}

.hero__circle_decor {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	background-color: rgba($blue, 0.02);
	border-radius: 50%;

	opacity: 0;
	will-change: transform;
}

.hero__line {
	position: absolute;
	top: 50%;
	left: 50%;

	width: 1px;
	height: 1px;

	will-change: transform;
	transform: rotate(-18deg);

	&::after {
		content: '';
		position: absolute;
		left: 0;
		z-index: 2;

		height: 1px;
		background: linear-gradient(112deg, rgba(0, 102, 221, 1) 0%, rgba(0, 102, 221, 0) 100%);

		@include bp($point_2, min) {
			width: 29rem;
		}

		@include bp($point_3) {
			width: 38vw;
		}
	}
}

.hero__center {
	position: relative;
	z-index: 3;

	display: flex;
	align-items: center;
	justify-content: center;

	border-radius: 50%;
	background-color: $blue_3;

	@include bp($point_2, min) {
		width: 5.8rem;
		height: 5.8rem;
	}

	@include bp($point_3) {
		width: 3rem;
		height: 3rem;
	}
}

.hero__icon {
	display: block;
	object-fit: contain;

	@include bp($point_2, min) {
		width: 2.5rem;
		height: 2.5rem;
	}

	@include bp($point_3) {
		width: 1.4rem;
		height: 1.4rem;
	}
}

.hero__card {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 3;

	aspect-ratio: 1 / 1;

	will-change: transform;
	transform: rotate(1deg);

	pointer-events: initial;

	@include bp($point_3) {
		font-size: calc(100vw / 1024 * 14);
	}

	&[data-inactive] {
		pointer-events: none;
	}

	&.hero__card--active_mod {
		z-index: 4;
	}

	&.hero__card--v0_mod {
		margin: -35.8% 0 0 -4.4%;
		width: 13.6em;
	}

	&.hero__card--v1_mod {
		margin: -28.8% 0 0 -44.8%;
		width: 18.6em;
	}

	&.hero__card--v2_mod {
		margin: -42.2% 0 0 -19.2%;
		width: 7.4em;
	}

	&.hero__card--v3_mod {
		margin: 5.6% 0 0 -36.8%;
		width: 10.4em;
	}

	&.hero__card--v4_mod {
		margin: 2.8% 0 0 7.8%;
		width: 17.2em;
	}

	&.hero__card--v5_mod {
		margin: -5.4% 0 0 -19.8%;
		width: 6.2em;
	}
}

.hero__sellers {
	@include bp($point_3) {
		justify-content: center;
	}
}
