.posts {
}

.posts__title {
	@include h2;

	color: $black;

	@include bp($point_3) {
		text-align: center;
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.posts__list {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.4rem;
		}
	}
}

.posts__info {
	display: flex;
	align-items: center;

	@include bp($point_2, min) {
		gap: 2rem;
	}

	@include bp($point_3) {
		flex-direction: column-reverse;
		gap: 1.6rem;
	}
}

.posts__result {
	@include text;

	color: $black_3;

	@include bp($point_2, min) {
		width: 33.3333%;
	}
}

.posts__btn {
	display: flex;
	justify-content: center;

	@include bp($point_2, min) {
		width: 33.3333%;
	}

	@include bp($point_3) {
		display: none;
	}
}

.posts__pagination {
	@include bp($point_2, min) {
		display: flex;
		justify-content: flex-end;
		width: 33.3333%;
	}
}
