.header_sublist {
	position: relative;

	@include bp($point_2, min) {
		display: flex;
		flex-wrap: wrap;

		padding: 0.8rem;
		background-color: $white;
		border-radius: 0.8rem;
		box-shadow: 0 0.9rem 2.8rem 0.8rem rgba($black, 0.05), 0 3px 0.6rem -0.4rem rgba($black, 0.12),
			0 0.6rem 1.6rem 0 rgba($black, 0.08);
	}

	@include bp($point_3) {
		padding-top: 2px;
		padding-bottom: 3.4rem;
	}

	&::before {
		@include bp($point_2, min) {
			content: '';
			position: absolute;
			z-index: 2;
			top: 0;
			left: 21%;
			transform: translate(-50%, -100%);

			border-width: 0 0.8rem 0.8rem 0.8rem;
			border-style: solid;
			border-color: transparent transparent $white transparent;
		}
	}
}

.header_sublist__item {
	@include bp($point_2, min) {
		width: 50%;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.header_sublist__link {
	display: flex;
	gap: 1.2rem;

	@include bp($point_2, min) {
		padding: 1.2rem;
		border-radius: 0.8rem;
		transition: background 0.3s;
	}

	@include bp($point_3) {
		transition: opacity 0.3s;
	}

	&:hover,
	&:focus {
		text-decoration: none;

		@include bp($point_3) {
			opacity: 0.8;
		}
	}

	@media (any-hover: hover) {
		&:hover {
			@include bp($point_2, min) {
				background-color: $black_4;
			}
		}
	}
}

.header_sublist__icon {
	flex-shrink: 0;
	display: block;
	width: 2.4rem;
	height: 2.4rem;
	color: $primary;
}

.header_sublist__wrap {
	display: block;
}

.header_sublist__title {
	display: block;
	font-size: 1.6rem;
	line-height: 1.5;
	font-weight: 500;
	color: $black_2;

	&:not(:last-child) {
		margin-bottom: 2px;
	}
}

.header_sublist__text {
	display: block;
	font-size: 1.4rem;
	line-height: 1.57;
	color: $black_3;
}
