.article_footer {
	display: flex;
	padding: 1.6rem 2.4rem;

	background-color: $black_5;
	border-radius: 1.2rem;
	border: 1px solid $border_secondary;

	@include bp($point_2, min) {
		align-items: center;
		gap: 2rem;
	}

	@include bp($point_3) {
		flex-direction: column;
		align-items: center;
		text-align: center;
	}
}

.article_footer__wrap {
	@include bp($point_2, min) {
		display: flex;
		align-items: center;
		gap: 1.2rem;

		max-width: 50%;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.article_footer__picture {
	display: block;
	width: 4rem;
	height: 4rem;
	flex-shrink: 0;
	border-radius: 50%;
	overflow: hidden;

	background-color: $black_6;

	@include bp($point_3) {
		margin-left: auto;
		margin-right: auto;
	}

	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.article_footer__img {
	@include cover_img;
}

.article_footer__name {
	@include h5;

	font-family: $font_1;
}

.article_footer__position {
	@include text_2;

	color: $black_3;
}

.article_footer__share {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	gap: .8rem;

	@include bp($point_2, min) {
		margin-left: auto;
	}

	@include bp($point_3) {
		justify-content: center;
	}
}

.article_footer__label {
	@include text_2;

	color: $black_3;
	flex-shrink: 0;

	@include bp($point_3) {
		width: 100%;
	}
}

.article_footer__link {
	display: flex;
	align-items: center;
	justify-content: center;

	flex-shrink: 0;
	width: 2.4rem;
	height: 2.4rem;
	border-radius: 50%;

	transition: background .3s ease;

	color: $primary;
	border: 1px solid $primary;

	&:hover,
	&:focus {
		background-color: $black_4;
	}

	&[data-copied] {
		pointer-events: none;
		color: $green;
		border-color: $green;
	}
}

.article_footer__icon {
	display: block;
	width: 1.4rem;
	height: 1.4rem;
}