.heading {
	max-width: 66.8rem;
	margin-left: auto;
	margin-right: auto;

	text-align: center;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}

	&.heading--offset_mod {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 3rem;
			}

			@include bp($point_3) {
				margin-bottom: 0.8rem;
			}
		}
	}

	&.heading--layout_2_mod {
		max-width: 50rem;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 4.2rem;
			}

			@include bp($point_3) {
				margin-bottom: 3.2rem;
			}
		}
	}

	&.heading--layout_3_mod {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 6.2rem;
			}

			@include bp($point_3) {
				margin-bottom: 3.6rem;
			}
		}
	}

	&.heading--full_mod {
		max-width: 100%;
	}
}

.heading__subtitle {
	@include category_label;

	color: rgba($black, 0.45);
	line-height: 1.57;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}

.heading__title {
	position: relative;
	display: inline-block;

	h1 {
		@include h1;
	}

	h2 {
		@include h2;
	}

	h3 {
		@include h3;
	}

	h4 {
		@include h4;
	}

	h5 {
		@include h5;
	}

	strong {
		@include word_decor;

		font-weight: inherit;

		&::after {
			@include bp($point_2, min) {
				top: 68%;
				left: 0.6rem;
			}
		}

		.hero_v2--v10_mod & {
			&::after {
				top: 71%;

				background-image: url(data:image/svg+xml;base64,);

				@include bp($point_2, min) {
					left: 0;

					width: 104%;
				}

				@include bp($point_3) {
					left: 1px;

					width: 99%;
				}
			}
		}
	}

	img {
		@include contain_img;

		position: absolute;
		z-index: -1;

		.heading--layout_1_mod & {
			transform: rotate(14deg);

			@include bp($point_2, min) {
				top: -3.2rem;
				right: -3.4rem;
				width: 7.4rem;
				height: 7.4rem;
			}

			@include bp($point_3) {
				top: -2.4rem;
				right: -3.6rem;
				width: 6.4rem;
				height: 6.4rem;
			}
		}

		.heading--layout_2_mod & {
			transform: rotate(22deg);

			@include bp($point_2, min) {
				top: -4.8rem;
				right: 2.6rem;
				width: 7.4rem;
				height: 7.4rem;
			}

			@include bp($point_3) {
				top: -3.6rem;
				right: 1.8rem;
				width: 6rem;
				height: 6rem;
			}
		}

		.heading--layout_3_mod & {
			transform: rotate(14deg);

			@include bp($point_2, min) {
				bottom: -3px;
				right: 11.8em;
				width: 7.4em;
				height: 7.4em;
			}

			@include bp($point_3, $point_5) {
				bottom: -1em;
				right: -1.6em;
				width: 7.4em;
				height: 7.4em;
			}

			@include bp($point_5 - 1) {
				bottom: -3px;
				right: 7.2em;
				width: 6.4em;
				height: 6.4em;
			}
		}
	}

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 0.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}

		.heading--layout_2_mod &,
		.heading--layout_4_mod & {
			@include bp($point_3) {
				margin-bottom: 0.8rem;
			}
		}
	}
}

.heading__text {
	@include text_3;

	color: $black_3;

	h3,
	h2 {
		font-weight: inherit;
	}

	p {
		&:not(:last-child) {
			margin-bottom: 0.8rem;
		}
	}

	.heading--layout_4_mod & {
		@include bp($point_2, min) {
			@include text_2;
		}

		@include bp($point_3) {
			font-size: 1.6rem;
			line-height: 1.5;
		}
	}
}

.heading__title_wrap {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 1.2rem;
}

.heading__icon {
	width: 4rem;
	height: 4rem;
	margin-top: -1rem;

	@include bp($point_3) {
		display: none;
	}
}

.heading__icon_img {
	position: relative;
	top: 0;
	left: 0;

	@include contain_img;
}
