.article_card {
	position: relative;

	width: 100%;
	height: 100%;

	border: 1px solid $border_secondary;
	border-radius: 0.8rem;
	background-color: $black_5;
	overflow: hidden;

	transition: background-color .3s, border-color .3s;

	&:hover,
	&:focus {
		background-color: $geekblue_2;
		border-color: $primary;
	}
}

.article_card__link {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	z-index: 2;
}

.article_card__media {
	display: block;
}

.article_card__pic {
	display: block;
	position: relative;

	width: 100%;
	overflow: hidden;

	@include bp($point_2, min) {
		padding-bottom: 85.2%;
	}

	@include bp($point_3) {
		padding-bottom: 45.2%;
	}
}

.article_card__img {
	@include stretch;
	@include cover_img;
}

.article_card__content {
	display: flex;
	flex-direction: column;

	@include bp($point_2, min) {
		padding: 2.6rem 2.2rem 2.4rem;
	}

	@include bp($point_3) {
		padding: 2.6rem 2.4rem 2.4rem;
	}
}

.article_card__category {
	@include text;

	position: relative;
	z-index: 3;

	display: block;

	font-family: $font_2;
	font-weight: 600;

	transition: opacity 0.3s ease;

	[data-category='0'] & {
		color: $primary;
	}

	[data-category='1'] & {
		color: $geekblue_8;
	}

	[data-category='2'] & {
		color: $cyan;
	}

	&:not(:last-child) {
		margin-bottom: 1.8rem;
	}

	&:hover,
	&:focus {
		opacity: 0.8;
		text-decoration: none;
	}
}

.article_card__title {
	@include h4;

	display: block;
	overflow: hidden;

	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	color: $black_2;

	transition: opacity 0.3s ease;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}

	&:hover,
	&:focus {
		opacity: 0.8;
		text-decoration: none;
	}
}

.article_card__text {
	@include text;

	flex-grow: 1;
	line-height: 1.5;
	color: $black_3;

	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}
}

.article_card__info {
	@include text;

	display: flex;
	align-items: center;
	flex-wrap: wrap;
	gap: 2rem;

	color: $black_8;
}

.article_card__date {
	display: flex;
	align-items: center;
	gap: 0.6rem;
}

.article_card__date_icon {
	display: block;
	width: 1.8rem;
	height: 1.8rem;
}

.article_card__reading_time {
	margin-left: auto;
}