.dropshipping_card {
	position: relative;
	overflow: hidden;
	height: 100%;
	width: 100%;
	min-height: 63rem;

	background-color: #f0f5ff;
	border: 1px solid #d6e4ff;

	@include bp($point_2, min) {
		padding: 3.2rem 3.1rem 24rem 3.2rem;
		border-radius: 1.2rem;
	}
	@include bp($point_3) {
		border-radius: 0.6rem;
		padding: 2.4rem 2.3rem 24rem;
	}

	// @include bp($point_3, $point_5) {
	// 	max-width: 40.6rem;
	// }

	// @include bp($point_5 - 1) {
	// 	max-width: 33rem;
	// }

	.dropshipping__item--md_mod & {
		@include bp($point_2, min) {
			min-height: 58rem;
		}

		@include bp($point_3) {
			min-height: 44rem;
		}
	}

	.dropshipping__item:nth-child(1) & {
		@include bp($point_2, min) {
			padding-bottom: 64.8%;
		}
		@include bp($point_3) {
			padding-bottom: 67.8%;
		}
	}
	.dropshipping__item:nth-child(2) & {
		@include bp($point_2, min) {
			padding-bottom: 52%;
		}
		@include bp($point_3) {
			padding-bottom: 74.8%;
		}
	}
	.dropshipping__item:nth-child(3) & {
		@include bp($point_2, min) {
			padding-bottom: 85%;
		}
		@include bp($point_3) {
			padding-bottom: 85%;
		}
	}
	.dropshipping__item:nth-child(4) & {
		@include bp($point_2, min) {
			padding-bottom: 85%;
		}
		@include bp($point_3) {
			padding-bottom: 98%;
		}
	}
	.dropshipping__item:nth-child(5) & {
		@include bp($point_2, min) {
			padding-bottom: 78%;
		}
		@include bp($point_3) {
			padding-bottom: 89.6%;
		}
	}
	.dropshipping__item:nth-child(6) & {
		@include bp($point_2, min) {
			padding-bottom: 91%;
		}
		@include bp($point_3) {
			padding-bottom: 102%;
		}
	}
	.dropshipping__item:nth-child(7) & {
		@include bp($point_2, min) {
			padding-bottom: 80%;
		}
		@include bp($point_3) {
			padding-bottom: 81%;
		}
	}
	.dropshipping__item:nth-child(8) & {
		@include bp($point_2, min) {
			padding-bottom: 91%;
		}
		@include bp($point_3) {
			padding-bottom: 104%;
		}
	}
	.dropshipping__item:nth-child(9) & {
		@include bp($point_2, min) {
			padding-bottom: 91%;
		}
		@include bp($point_3) {
			padding-bottom: 107%;
		}
	}

	&.dropshipping_card--size_mod.dropshipping_card--size_mod {
		@include bp($point_2, min) {
			display: flex;
			align-items: center;

			min-height: 42rem;
			padding: 3.2rem;
		}
		@include bp($point_3) {
			min-height: 64.8rem;
		}
	}
}

.dropshipping_card__content {
	z-index: 2;
	position: relative;
	.dropshipping_card--size_mod & {
		@include bp($point_2, min) {
			max-width: 51.75%;
		}
	}
}

.dropshipping_card__title {
	font-size: 2.4rem;
	font-weight: 600;
	color: rgba($black, 0.88);
	font-family: $font_2;

	&:not(:last-child) {
		margin-bottom: 0.6rem;
	}

	@include bp($point_2, min) {
		line-height: 1.3;
	}
	@include bp($point_3) {
		line-height: 1.35;
		&:not(:last-child) {
			margin-bottom: 3px;
		}
	}

	.dropshipping_card--size_mod & {
		@include bp($point_2, min) {
			&:not(:last-child) {
				margin-bottom: 1rem;
			}
		}
	}
	.dropshipping__item--md_mod & {
		@include bp($point_2, min) {
			&:not(:last-child) {
				margin-bottom: 4px;
			}
		}
	}
}

.dropshipping_card__descr {
	font-size: 1.6rem;
	color: rgba($black, 0.65);
	line-height: 1.5;
	font-weight: 400;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
	.dropshipping_card--size_mod & {
		@include bp($point_2, min) {
			max-width: 98%;
		}

		&:not(:last-child) {
			@include bp($point_3) {
				margin-bottom: 1.6rem;
			}
		}
	}

	.dropshipping__item--md_mod & {
		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}
}

.dropshipping_card__link {
}

.dropshipping_card__pic {
	position: absolute;
	display: block;

	@include bp($point_2, min) {
		bottom: 0;
		right: 0;
		width: 100%;
	}
	@include bp($point_3) {
		bottom: 0;
		left: 50%;
		width: 101%;
		transform: translateX(-50%);
	}

	.dropshipping__item:nth-child(5) & {
		@include bp($point_3) {
			width: 123%;
		}
	}

	.dropshipping_card--size_mod & {
		@include bp($point_2, min) {
			width: 47%;
		}
		@include bp($point_3) {
			width: 100%;
			bottom: 0;
			right: auto;
		}
	}
}
.dropshipping_card__img {
	@include cover_img;
	.dropshipping_card--size_mod & {
		object-fit: contain;
	}
}
