@import 'family';
@import 'reset';
@import 'fonts';
@import 'print';

//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--height-header: 7.2rem;
	--height-footer: 10rem;

	--gl-hr-indent: 2rem;

	@include bp($point_2, min) {
		--gl-vr-indent: 4.4rem;
	}

	@include bp($point_3) {
		--gl-vr-indent: 3.2rem;
	}
}

h1,
h2,
h3 {
	a {
		white-space: break-spaces;
		color: inherit;
		display: inline;
		&:hover,
		&:focus {
			text-decoration: none;
		}
	}

	strong {
		display: inline;
	}
}

html {
	--height-timer-banner: 6.4rem;

	height: 100%;
	font-weight: 400;
	line-height: 1.4;
	font-family: $font_1;
	font-size: 10px;

	@include bp($point_0, $point_2) {
		font-size: calc(100vw / 1440 * 10);
	}

	@include bp($point_7 - 1) {
		font-size: calc(100vw / 375 * 10);
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;

	font-size: $font_size_base;
	color: $color_text_base;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $body_bg;

	@supports (display: grid) {
		display: block;
	}

	&.body--open_menu_state {
		overflow: hidden;
	}
	&.body--shopify_banner_mod {
		@include bp($point_2, min) {
			padding-top: 4.6rem;
		}

		@include bp($point_3) {
			padding-top: 10.9rem;
		}
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;
	padding: var(--height-header) 0 var(--gl-vr-indent);

	flex: 1 0 auto;

	.body--error_page_mod & {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	.body--timer_banner_mod & {
		padding: calc(var(--height-header) + var(--height-timer-banner)) 0 var(--gl-vr-indent);
	}
}

a {
	text-decoration: none;
	color: $color_link_base;

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

// for wp

.editor-styles-wrapper .wp-block {
	margin-bottom: 5rem;
}

html :where(.wp-block) {
	max-width: 1200px;
}

.editor-styles-wrapper {
	overflow-x: hidden;
}

.back_button {
	@include text;

	display: inline-flex;

	font-family: $font_2;

	color: $black;

	transition: color 0.3s;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}

	&:hover,
	&:focus {
		text-decoration: none;
		color: $primary;
	}
}

//------------------------------------------------------------base styles###

.fancybox__backdrop {
	--fancybox-bg: #fff;
}

.fancybox__caption {
	--fancybox-color: #000;
}

.fancybox_link {
	padding: 0 1rem;
}
