.featured_in {
	padding-bottom: calc(var(--gl-vr-indent) + 1.2rem);

	&:not(.featured_in--items_indent_mod) {
		padding-bottom: calc(var(--gl-vr-indent) + 0.8rem);
		padding-top: var(--gl-vr-indent);
	}
}

.featured_in__label {
	color: rgba($black, 0.45);
	text-align: center;

	@include category_label;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}

	>* {
		font-weight: inherit;
	}
}

.featured_in__row {
	@include flex_block(row, wrap, center, center);

	@include bp($point_2, min) {
		margin: 0 -4.8rem -3rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.6rem -2rem 0;
	}

	.featured_in--items_indent_mod & {
		@include bp($point_2, min) {
			margin-right: -9rem;
		}
	}
}

.featured_in__col {
	@include bp($point_2, min) {
		padding: 0 4.8rem 3rem 0;
	}

	@include bp($point_3) {
		flex: 0 1 20%;
		padding: 0 1.6rem 2rem 0;

		text-align: center;
	}

	@include bp($point_3, $point_6) {
		min-width: 14rem;
	}

	@include bp($point_6 - 1) {
		min-width: 11.6rem;
	}

	.featured_in--items_indent_mod & {
		@include bp($point_2, min) {
			padding-right: 9rem;
		}
	}
}

.featured_in__logo_w {
	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_4, min) {
		max-width: 16.7rem;
		height: 3.2rem;
	}

	@include bp($point_4 - 1) {
		height: 2.5rem;
	}
}

.featured_in__logo_in {
	width: auto !important;
	max-width: 100%;
	max-height: 100%;
	object-fit: contain;
}