.feature {
	&+.feature {
		@include bp($point_4, min) {
			padding-top: 1.2rem;
		}

		@include bp($point_4 - 1) {
			padding-top: 3.2rem;
		}
	}
}

.feature__row {
	margin: 0 -2.4rem -3rem 0;

	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, center, center);
	}
}

.feature__column {
	padding: 0 2.4rem 3rem 0;

	@include bp($point_4, min) {
		flex-shrink: 0;
		width: 50%;
	}

	&.feature__column--content_mod {
		.feature--reverse_mod & {
			@include bp($point_4, min) {
				order: 1;
			}
		}
	}

	&.feature__column--image_mod {
		@include bp($point_4 - 1) {
			max-width: 64.4rem;
			margin: 0 auto;
		}

		.feature--reverse_mod & {
			@include bp($point_4, min) {
				order: 2;
			}
		}
	}
}

.feature__content {
	.heading {
		@include bp($point_4, min) {
			margin-left: 0;
			margin-right: 0;
			text-align: left;
		}

		&:not(:last-child) {
			@include bp($point_3) {
				margin-bottom: 3rem;
			}
		}

		h2 {
			@include bp($point_4, min) {
				max-width: 59.2rem;
			}
		}
	}
}

.feature__btn_w {
	@include bp($point_4 - 1) {
		text-align: center;
	}
}

.feature__img_w {
	display: block;
	aspect-ratio: 620/486;

	.feature--small_image_mod & {
		aspect-ratio: 620/400;
	}

	.feature--large_image_mod & {
		@include bp($point_4, min) {
			width: 108%;
			margin-left: -1.6%;
		}
	}
}

.feature__img_in {
	@include contain_img;

	&:not(.feature--reverse_mod &) {
		@include bp($point_4, min) {
			object-position: left;
		}
	}
}