$transition: 0.3s ease;

.article {
}

.article__row {
	@include bp($point_2, min) {
		display: flex;

		margin: 0 -4.2rem -4.2rem 0;
	}
}

.article__column {
	@include bp($point_2, min) {
		padding: 0 4.2rem 4.2rem 0;
	}

	&:nth-child(3n + 1) {
		@include bp($point_2, min) {
			width: 31.2rem;
			flex-shrink: 0;
		}

		@include bp($point_3) {
			display: none;
		}
	}

	&:nth-child(3n + 3) {
		@include bp($point_2, min) {
			width: 31.2rem;
			flex-shrink: 0;
		}

		@include bp($point_3) {
			display: none;
		}
	}

	&:nth-child(3n + 2) {
		@include bp($point_2, min) {
			flex: 1 1 auto;
		}
	}
}

.article__aside {
	will-change: transform;
}

.article__label {
	@include text_2;

	color: rgba($black, 0.45);

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}
}

.article__list {
	position: relative;

	max-height: calc(var(--full-screen) - var(--height-header) - var(--gl-vr-indent) * 2);
	overflow-x: hidden;
	overflow-y: auto;
}

.article__link {
	@include text_2;

	position: relative;

	display: block;
	padding: 0.9rem 2.4rem;
	min-height: 3.8rem;

	color: $black_2;

	transition: color $transition, background $transition;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;

		width: 2px;
		height: 100%;

		background-color: $gray_6;
		pointer-events: none;
		transform: scaleX(0.5);

		transition: background $transition, transform $transition, text-shadow $transition;
	}

	&.article__link--active_state {
		pointer-events: none;
		color: $primary;
		text-shadow: 0.4px 0 0 currentColor, -0.4px 0 0 currentColor;

		&::before {
			background-color: $primary;
			transform: scaleX(1);
		}
	}

	@media (any-hover: hover) {
		&:hover {
			background: $black_4;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.article__title {
	@include h2;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}
}

.article__text {
	@include text_2;

	color: $black_3;

	&:not(:last-child) {
		margin-bottom: 3.4rem;
	}
}
