.guarantee__content {
	border-radius: 0.8rem;
	border: 1px solid $border_secondary;
	overflow: hidden;

	@include bp($point_4, min) {
		display: flex;
	}
}

.guarantee__media {
	display: flex;
	justify-content: center;
	align-items: center;

	background-color: $purple_1;

	@include bp($point_4, min) {
		padding: 5.8rem 10.4rem;
	}

	@include bp($point_4 - 1) {
		padding: 5.2rem 10.6rem;
	}
}

.guarantee__img {
	display: block;
	object-fit: contain;

	@include bp($point_4, min) {
		width: 13.8rem;
		height: 16.2rem;
	}

	@include bp($point_4 - 1) {
		width: 12.4rem;
		height: 14.6rem;
	}
}

.guarantee__info {
	background-color: $black_5;

	@include bp($point_2, min) {
		padding: 5.2rem 4.8rem 4.4rem;
	}

	@include bp($point_3) {
		padding: 2.4rem;
	}

	h2 {
		@include h2;

		line-height: 1.44;
		color: $black_2;

		@include bp($point_4 - 1) {
			text-align: center;
		}

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 0.8rem;
			}

			@include bp($point_3) {
				margin-bottom: 1.6rem;
			}
		}
	}

	p {
		color: $black_3;

		@include bp($point_2, min) {
			font-size: 2rem;
			line-height: 1.4;
		}

		@include bp($point_3) {
			font-size: 1.6rem;
			line-height: 1.5;
		}

		@include bp($point_4 - 1) {
			text-align: center;
		}

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}
}

.guarantee__btn {
	@include bp($point_4 - 1) {
		width: 100%;
	}
}
