@mixin st_btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	gap: 0.8rem;

	text-align: center;
	white-space: nowrap;
	font-family: $font_1;

	cursor: pointer;

	&[type='submit'] {
		@include appearance(none);
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//color mixins:
@mixin btn_style_blue {
	font-weight: 500;
	color: $white;
	background-color: $primary;
	box-shadow: 0 2px 0 0 rgba(5, 145, 255, 0.1);
	transition: background-color 0.3s ease;

	@include st_btn;

	&:hover {
		background-color: $primary_hover;
	}
}

@mixin btn_style_blue_2 {
	font-weight: 600;
	color: $primary;
	background-color: transparent;
	box-shadow: 0 2px 0 0 rgba($black, 0.02);
	transition: color 0.3s ease, background-color 0.3s;
	border: 1px solid $primary;

	@include st_btn;

	&:hover {
		color: $white;
		background-color: $primary;
	}
}

@mixin btn_style_grey {
	font-weight: 500;
	color: $black_2;
	background-color: $white;
	box-shadow: 0 2px 0 0 rgba($black, 0.02);
	transition: color 0.3s ease, border-color 0.3s;
	border: 1px solid $gray_6;

	@include st_btn;

	&:hover {
		color: $primary;
		border-color: $primary;
	}
}

@mixin btn_style_grey_2 {
	font-weight: 500;
	color: $black_2;
	background-color: $white;
	box-shadow: 0 2px 0 0 rgba($black, 0.02);
	transition: color 0.3s ease, border-color 0.3s ease;
	border: 1px solid $gray_6;

	@include st_btn;

	&:hover {
		color: $primary;
		border-color: $primary;
	}
}

//size mixins:
@mixin btn_size_normal {
	height: 4rem;
	padding: 0 1.5rem;

	line-height: 4rem;
	font-size: 1.6rem;

	border-radius: $gl_radius_2;
}