.articles__list {
	@include bp($point_4, min) {
		display: flex;
		flex-wrap: wrap;
		margin-right: -2.4rem;
		margin-bottom: -2.4rem;
	}
}

.articles__item {
	@include bp($point_2, min) {
		width: 25%;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
	}

	@include bp($point_4, min) {
		padding-right: 2.4rem;
		padding-bottom: 2.4rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}

	&:not(:last-child) {
		@include bp($point_4 - 1) {
			margin-bottom: 1.8rem;
		}
	}
}
