.btn_base {
	@include btn_style_blue;
	@include btn_size_normal;
}

.btn_v1 {
	@include btn_style_grey;
	@include btn_size_normal;

	&.btn_v1--disabled_mod {
		color: $black_6;

		background-color: $black_7;
		pointer-events: none;
	}

	&:hover {
		.btn_icon {
			filter: invert(29%) sepia(94%) saturate(1861%) hue-rotate(206deg) brightness(103%) contrast(101%);
		}
	}

	.btn_icon {
		flex-shrink: 0;
		width: 1.6rem;
		height: 1.6rem;
	}
}

.btn_v2 {
	@include btn_style_grey_2;
	@include btn_size_normal;

	&.btn_v2--disabled_mod {
		color: $black_6;

		background-color: $black_7;
		pointer-events: none;
	}
}

.btn_v3 {
	@include btn_style_blue_2;
	@include btn_size_normal;
}

.btn_mobile {
	@include bp($point_2, min) {
		display: none;
	}
}

.btn_desktop {
	@include bp($point_3) {
		display: none;
	}
}
