.filters {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.2rem;
		}
	}
}

.filters_in {
	display: flex;
	flex-wrap: wrap;

	@include bp($point_2, min) {
		align-items: flex-end;
		gap: 2.4rem;
	}

	@include bp($point_3) {
		flex-direction: column;
		gap: 1.6rem;
	}

	&:not(:last-child) {
		margin-bottom: 1.8rem;
	}
}

.filters__input {
	@include bp($point_3) {
		order: 2;
	}
}

.filters__btn {
	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}

	&.filters__btn--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.filters__btn_icon {
	display: block;
	width: 1.6rem;
	height: 1.6rem;
}

.filters__selects {
	display: flex;
	flex-grow: 1;

	@include bp($point_2, min) {
		align-items: flex-end;
	}

	@include bp($point_3) {
		display: none;
	}

	&.filters__selects--open_state {
		@include bp($point_3) {
			display: block;
		}
	}
}

.filters__select {
	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 1.6rem;
		}
	}

	&.filters__select--category_mod {
		@include bp($point_2, min) {
			min-width: 38.4rem;
		}
	}

	&.filters__select--showing_mod {
		@include bp($point_2, min) {
			display: flex;
			align-items: center;
			gap: 1.2rem;
			margin-left: auto;
		}
	}
}

.filters__select_label {
	font-weight: 600;
	font-size: 1.6rem;
	color: $black_2;

	&:not(:last-child) {
		margin-bottom: 0.4rem;
	}

	.filters__select--showing_mod & {
		@include bp($point_2, min) {
			color: $black_3;
		}
	}
}

.filters__select_btn_w {
	position: relative;

	.filters__select--showing_mod & {
		min-width: 8.4rem;
	}
}

.filters__select_btn {
	position: relative;
	width: 100%;
	height: 4rem;
	padding: 0 3.4rem 0 1rem;

	font-family: inherit;
	font-size: 1.6rem;
	text-align: left;
	color: $black_2;

	border: 1px solid $gray_6;
	border-radius: 0.8rem;
	box-shadow: 0 2px 2px rgba(0, 0, 0, 0.02);
	background-color: $white;

	transition: border-color 0.3s ease, box-shadow 0.3s ease;
	cursor: pointer;

	&:hover,
	&:focus {
		border-color: $primary;
		box-shadow: 0 0 0 2px rgba(5, 145, 255, 0.1);
	}

	&::after {
		position: absolute;
		content: '';
		z-index: 1;
		bottom: 1rem;
		right: 1rem;
		display: block;
		width: 1.6rem;
		height: 1.6rem;
		background-image: url('data:image/svg+xml;base64,');
	}
}

.filters__select_list {
	position: absolute;
	z-index: 10;
	top: calc(100% + 0.6rem);
	left: 0;
	width: 100%;
	padding: 4px;

	border-radius: 0.6rem;
	background-color: $white;
	box-shadow: 0 0.9rem 2.8rem 0.8rem rgba(0, 0, 0, 0.05), 0 3px 0.6rem -4px rgba(0, 0, 0, 0.12),
		0 0.6rem 1.6rem 0 rgba(0, 0, 0, 0.08);
	pointer-events: none;
	opacity: 0;
	transform: translateY(-10%);

	transition: opacity 0.3s ease, transform 0.3s ease;

	&.filters__select_list--open_state {
		pointer-events: initial;
		opacity: 1;
		transform: translateY(0);
	}
}

.filters__categories {
	display: none;

	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}

	&.filters__categories--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}

	&.filters__categories--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}

	&.filters__categories--open_state {
		&.filters__categories--desktop_mod {
			@include bp($point_2, min) {
				display: block;
			}
		}

		&.filters__categories--mobile_mod {
			@include bp($point_3) {
				display: block;
			}
		}
	}
}

.filters__categories_title {
	font-size: 1.2rem;
	color: $black_3;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}
}

.filters__categories_list {
	display: flex;
	gap: 0.8rem;
	flex-wrap: wrap;
}

.filters__categories_item {
	display: flex;
	align-items: center;
	gap: 4px;
	height: 2.2rem;
	padding: 00.8rem;

	font-family: inherit;
	color: $black_2;

	background-color: $black_5;
	border: 1px solid $gray_6;
	border-radius: 4px;
}

.filters__categories_category_text {
	display: block;
	padding-top: 1px;

	font-size: 1.2rem;
	line-height: 1;
}

.filters__categories_category_btn {
	display: block;
	width: 1.2rem;
	height: 1.2rem;

	color: $black_2;

	border-radius: 2px;
	cursor: pointer;

	transition: background-color 0.3s ease;

	&:hover,
	&:focus {
		background-color: $gray_6;
	}
}
