.pricing_hero {
	@include bp($point_2, min) {
		padding-top: 6.8rem;
	}

	@include bp($point_3) {
		padding-top: 5.2rem;
	}
}

.pricing_hero__list_w {}

.pricing_hero__list {
	@include flex_block(row, wrap, flex-start, stretch);

	@include bp($point_2, min) {
		margin: 0 -2.4rem -2.4rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.6rem -1.6rem 0;
	}
}

.pricing_hero__item {
	position: relative;

	flex-shrink: 0;

	@include bp($point_2, min) {
		padding: 0 2.4rem 2.4rem 0;
		width: 25%;
	}

	@include bp($point_3) {
		padding: 0 1.6rem 1.6rem 0;
	}

	@include bp($point_3, $point_5) {
		width: 50%;
	}

	@include bp($point_5 - 1) {
		width: 100%;
		max-width: 450px;
		margin: 0 auto;
	}
}

.pricing_hero__card_info {
	position: absolute;
	top: -9.4rem;
	left: 5.1rem;
	max-width: 17rem;

	font-family: $font_3;
	font-size: 1.4rem;
	line-height: 1.28;
	color: $black_3;

	transform: translateX(-100%);

	@include bp($point_3) {
		display: none;
	}
}

.pricing_hero__card_info_decor {
	position: absolute;
	bottom: -3.3rem;
	right: 4.8rem;

	display: block;
	width: 3rem;
	height: 3.2rem;
}

//===CARD=====================================================================================================================================================

.pricing_hero__card {
	position: relative;

	display: flex;
	flex-direction: column;
	padding: 2.4rem;
	height: 100%;
	min-height: 30.5rem;

	border: 1px solid rgba($black, 0.06);
	background-color: rgba($black, 0.02);
	border-radius: $gl_radius_2;

	&.pricing_hero__card--color_v1_mod {
		border-color: $border_secondary;
		background-color: $white;
	}

	&.pricing_hero__card--color_v2_mod {}

	&.pricing_hero__card--color_v3_mod {
		border-color: $color_info_border;
	}

	&.pricing_hero__card--color_v4_mod {
		border-color: $purple_3;
	}
}

.pricing_hero__card_label {
	position: absolute;
	top: 0;
	right: 2.4rem;

	display: flex;
	align-items: center;
	padding: 1px 0.8rem;

	background-color: $white;
	border: 1px solid rgba($black, 0.06);
	border-radius: 4px;

	transform: translateY(-50%);

	@include bp($point_2, min) {
		max-width: 83%;
	}

	@include bp($point_3) {
		max-width: 71%;
	}

	.pricing_hero__card--color_v1_mod & {
		border-color: $border_secondary;
	}

	.pricing_hero__card--color_v3_mod & {
		color: $primary;

		border-color: $color_info_border;
		background-color: $color_info_bg;

		@include bp($point_3) {
			right: 6rem;
		}
	}

	.pricing_hero__card--color_v4_mod & {
		color: $purple_4;

		border-color: $purple_3;
		background-color: $purple_1;

		@include bp($point_3) {
			max-width: 81%;
		}

		@include bp($point_3) {
			right: 2.4rem;
		}
	}
}

.card_label__icon_card_label {
	width: 1.2rem;
	height: 1.2rem;
	padding: 1px;

	&:not(:last-child) {
		margin-right: 4px;
	}
}

.card_label__text {
	display: flex;
	align-items: center;
	gap: 4px;

	font-size: 1.2rem;
	line-height: 1.67;

	&[data-current-time]::after {
		flex-shrink: 0;
		min-width: 5.4rem;

		content: attr(data-current-time);
	}
}

//===CARD-TOP=====================================================================================================================================================

.pricing_hero__top {
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.pricing_hero__label {
	display: flex;
	align-items: center;

	@include h4;

	line-height: 1.4;

	.pricing_hero__card--color_v1_mod & {
		color: $black_3;
	}

	&:not(:last-child) {
		margin-bottom: 2.4rem;
	}
}

.pricing_hero__label_icon {
	flex-shrink: 0;
	width: 2.8rem;
	height: 2.8rem;

	&:not(:last-child) {
		margin-right: 1.2rem;
	}
}

.pricing_hero__label_icon_in {
	@include contain_img;
}

.pricing_hero__label_icon_in img {
	@include contain_img;
}

.pricing_hero__label_text {}

.pricing_hero__price_block {
	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}
}

.pricing_hero__price_top_block {
	@include flex_block(row, nowrap, flex-start, center, center);

	&:last-child {
		padding-bottom: 2.8rem;
	}
}

.pricing_hero__price {
	display: flex;
	align-items: baseline;

	&:not(:last-child) {
		margin-right: 0.5rem;
	}
}

.pricing_hero__value {
	@include h2;

	color: rgba($black, 0.88);

	@include bp($point_3) {
		font-size: 3.6rem;
	}

	&:not(:last-child) {
		margin-right: 4px;
	}
}

.pricing_hero__per_time {
	font-size: 1.4rem;
	line-height: 1.07;
	color: rgba($black, 0.65);

	&::before {
		content: '/';
	}
}

.pricing_hero__discount {
	margin-left: auto;
	padding: 1px 0.8rem;

	color: $white;
	font-size: 1.2rem;
	line-height: 1.67;

	background-color: $red;
	border-radius: 4px;
}

//===CARD-BOTTOM=====================================================================================================================================================

.pricing_hero__bottom {}

.pricing_hero__btn_block {
	margin-top: auto;

	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}

	a {
		display: block;
	}
}

.pricing_hero__info_text {
	font-size: 1.4rem;
	line-height: 1.57;
	color: rgba($black, 0.65);

	p {
		&:not(:last-child) {
			margin-bottom: 4px;
		}
	}
}

.pricing_hero__old_price {
	@include h4;

	text-decoration: line-through;
	color: rgba($black, 0.65);
}

.pricing_hero__colored_label {
	display: inline-block;

	font-family: $font_2;
	font-weight: 600;
	font-size: 2rem;
	line-height: 1.4;

	.body--color_v1_mod & {
		background: linear-gradient(90deg, #9d18ff 0%, #fb1e7b 100%);
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}

	.body--color_v2_mod & {
		background: linear-gradient(135deg, #079d16 0%, #81ac08 100%);
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}

	.body--color_v3_mod & {
		background: linear-gradient(138deg, #3484e2 0%, #2dccef 100%);
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}

	.body--color_v4_mod & {
		background: linear-gradient(90deg, #c7150a 0%, #fc6e31 100%);
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}

	.body--color_v5_mod & {
		background: linear-gradient(90deg, #ed6604 0%, #ee9d01 100%);
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}
}