.form_select__label {
	@include form_el_label;
}

.form_select__label_v2 {
	@include form_el_label;

	color: $black_3;
	margin-bottom: 0;
}

.form_select__element {
	@include form_el_default;

	min-width: 8.4rem;
	padding: 0 2.4rem 0 1rem;
}

.form_select__field {
	position: relative;

	&::after {
		position: absolute;
		content: '';
		z-index: 10;
		bottom: 1.2rem;
		right: 1.2rem;
		display: block;
		width: 1.6rem;
		height: 1.6rem;
		background-image: url('data:image/svg+xml;base64,');
	}
}
