.stories_card {
	display: flex;
	flex-direction: column;
	flex-grow: 1;

	background-color: $geekblue_2;
	border: 1px solid $geekblue_1;
	border-radius: $gl_radius_2;
	overflow: hidden;

	min-height: 51.7rem;
}

.stories_card__decor_icon {
	position: absolute;
	bottom: -1.2rem;
	right: -0.8rem;

	width: 7.2rem;
	height: 7.2rem;

	color: $geekblue_3;
}

.stories_card__img_w {
	position: relative;

	display: block;
	aspect-ratio: 299/302;
}

.stories_card__img_in {
	@include stretch;
	@include cover_img;
}

.stories_card__content {
	position: relative;

	display: flex;
	flex-direction: column;
	flex-grow: 1;
	padding: 1.8rem 2rem 2rem 2rem;
}

.stories_card__top {
	position: relative;
	z-index: 1;

	flex-grow: 1;

	font-size: 1.6rem;
	line-height: 1.5;

	&:not(:last-child) {
		margin-bottom: 1.2rem;
	}
}

.stories_card__text {
	color: $black_2;

	overflow: hidden;

	.stories_card__top--init_state & {
		transition: max-height 0.5s ease;
	}

	&:not(.stories_card__top--open_state &) {
		@include overflow-text(4);
	}
}

.stories_card__more {
	font-family: inherit;
	color: $black_3;

	cursor: pointer;
	transition: color 0.3s ease;

	&:not(.stories_card__top--init_state &) {
		display: none;
	}

	&::before {
		content: attr(data-more-text);
	}

	&::after {
		content: attr(data-less-text);
	}

	&:not(.stories_card__top--open_state &) {
		&::after {
			display: none;
		}
	}

	.stories_card__top--open_state & {
		&::before {
			display: none;
		}
	}

	&:hover,
	&:focus {
		color: $primary;
	}
}

.stories_card__card_bottom {
	position: relative;
	z-index: 1;
}

.stories_card__name {
	@include h5;
}

.stories_card__prof {
	font-size: 1.4rem;
	line-height: 1.57;
	color: $black_3;
}
